// candlestick  ticker 
import _ from 'lodash'
import { ENUM_INTERVAL_TYPE } from 'utils/enum'
import api from 'api'
import utils from 'utils'

const initState = {
  dayInfo: {
    "high": 0,
    "low": 0,
    "size": 0,
    "volume": 0,
    "open": 0,
    "close": 0,
    "differ": 0,
    "variation": 0
  },
}

function formatTicker (ticker) {
  let { market: marketName, open, high, low, close, size, volume } = ticker

  const [baseTokenSymbol, quoteTokenSymbol] = marketName.split('-')

  size = utils.fromWEI(baseTokenSymbol, size)
  volume = utils.fromWEI(quoteTokenSymbol, volume)

  open = Number(open)
  close = Number(close)
  let differ = close - open
  let variation = open ? differ / open : 0

  return { high, low, size, volume, open, close, differ, variation }
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchTicker (payload, { markets, dexAccount }) {
      if (!markets.currentMarket) return
      let { market: marketPair } = markets.currentMarket
      
      //  1.  ticker 
      dispatch.socket.unsubscribe([{
        topic: 'ticker',
        unsubscribeAll: true
      }])
      dispatch.socket.subscribe([{
        topic: 'ticker',
        market: marketPair,
      }])
      
      let { apiKey } = dexAccount
      let tickers = await api.getTickers({
        markets: [marketPair],
        apiKey
      })

      if (!tickers || tickers.length === 0) return

      let dayInfo = formatTicker(tickers[0])
      this.update({
        dayInfo: dayInfo,
      })
    },
    async incrementalUpdate(payload, { candleStick: {tvSubscribers} }) {
      let { topic, data: ticker } = payload

      let [ market, timestamp, size, volume, open, high, low, close, count, bid, ask ] = ticker
      ticker = { market, timestamp, size, volume, open, high, low, close, count, bid, ask }
      let dayInfo = formatTicker(ticker)
      this.update({
        dayInfo: dayInfo,
      })
    }
  })
}