import _ from 'lodash'
import moment from "moment"
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { injectIntl, FormattedMessage  } from 'react-intl'
import { Modal, Input, Form, Button, Checkbox, Slider } from 'antd'
import {
  Box,
  Flex,
  Text
} from 'rebass'
import { Account, config, common, Utils } from 'librarys/lightcone_v3';
import funcfy from './funcfyDialog'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import utils, { notification, renderIf } from 'utils'
import api from 'api'
import { FormErrorInfo } from 'widgets/Common'
import BigNumber from "bignumber.js"

const gasLimitInWEI = BigNumber(config.getGasLimitByType("create").gasInWEI)
const createAccountFee = BigNumber(utils.fromWEI('ETH', config.getFeeByType('create').feeInWEI))

@injectIntl
class Widget extends Component {
  constructor(props) {
    super(props)

    this.state = {
      errors: [],
      visible: false,
      recommendedGasEth: [0, 0, 0],
    }

    let { intl } = props
    this.errorInfos = [
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.upperCase'}),
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.lowerCase'}),
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.number'}),
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.min'}),
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.pwdSame'})
    ]
  }

  componentDidMount () {
    this.setState({ visible: true })
    this.checkInput()
    this.props.fetchWalletBalances()

    api.getRecommendedGasPrice().then(resp => {
      let [min, normal, max] = resp
      let minGas = utils.fromWEI('ETH', BigNumber(min).times(gasLimitInWEI))
      let normalGas = utils.fromWEI('ETH', BigNumber(normal).times(gasLimitInWEI))
      let maxGas = utils.fromWEI('ETH', BigNumber(max).times(gasLimitInWEI))

      this.setState({recommendedGasEth: [Number(minGas), Number(normalGas), Number(maxGas)]})
    })
  }

  async checkInput () {
    let { intl } = this.props

    // onChangeonChange
    await utils.waitFor(50)

    // 
    let password = this.props.form.getFieldProps('password').value
    if (!password) {
      this.setState({errors: this.errorInfos})
      return 
    }
    
    let errors = this.props.form.getFieldsError()
    console.info(errors)
    errors = _.chain(errors)
    .values()
    .flatten()
    .filter(error => error)
    .value()
    
    let confirmPassword = this.props.form.getFieldProps('confirmPassword').value
    if (password !== confirmPassword) errors.push(intl.formatMessage({id: 'modal.upsertPwd.errorInfo.pwdSame'}))
    this.setState({errors})
  }

  async submit (e) {
    e.preventDefault();
    let { intl } = this.props

    let validateFields = utils.promisefy(this.props.form.validateFields)
    let values
    try {
      values = await validateFields()
    } catch(err) {
      return
    }
    // let { password, remember, gasEth = 0 } = values
    // gasEth 
    let { password, remember } = values
    let gasEth = this.state.recommendedGasEth[1]
    let ethAvail = this.getEthAvail()
    console.info({gasEth, ethAvail})
    // if (gasEth >= ethAvail) return
  
    let { type = 'modify', dexAccount, updateAccount, checkAccountCreated } = this.props
    const passwordHash = utils.genPasswordHash(dexAccount.address, password);
    const nonce = await api.getNonce(window.wallet.account);
    const exchangeAccount = new Account(window.wallet);

    let gasEthInWEI = utils.toWEI('ETH', gasEth)
    let gasPriceInWEI = BigNumber(gasEthInWEI).dividedBy(gasLimitInWEI)
    const resp = await exchangeAccount.createOrUpdateAccount(
      passwordHash,
      nonce,
      common.formatter.toGWEI(gasPriceInWEI)
    );
    
    const signedEthereumTx = resp['signedTx'];
    const keyPair = resp['keyPair'];
    
    if (keyPair.secretKey === undefined) {
      throw new Error(
        'Failed to parse the response of account creation.'
      );
    }
    
    // console.log('signedEthereumTx: ', signedEthereumTx);
    // const response = await api.sendDeposit(signedEthereumTx);
    // if (!response) return
    // const data = response['result'];
    // // TODO: Remove response
    // console.log('Response of createOrUpdateAccount: ', data);

    notification['success']({
      message: intl.formatMessage({id: `notify.account.${type}.success`})
    });

    // 
    //  apikey 
    if (type === 'create') {
      updateAccount({
        // TODO: accountId store?
        // accountId: relayAccount.accountId,
        state: ENUM_ACCOUNT_STATUS.CREATING.getValue(),
        passwordHash,
        rememberMeStartTime: moment().format('YYYY-MM-DD HH:mm:ss'),
        rememberMeMinutes: (remember ? 24 : 1) * 60,
      })
      checkAccountCreated()
    }

    this.loginSunccess()
  }

  async loginSunccess () {
    this.setState({visible: false})
    this.props.onSucess()
    this.props.onDestory()
  }

  getEthAvail () {
    let { type = 'modify', walletBalance } = this.props
    let ethBalance = _.find(walletBalance.list, b => b.symbol === 'ETH')
    let ethAmount = ethBalance ? ethBalance.amount : 0
    if (type === 'create') {
      return _.max(ethAmount - createAccountFee, 0)
    } else {
      return ethAmount
    }
  }

  render () {
    let { type = 'modify', intl } = this.props
    const { getFieldDecorator, getFieldsValue } = this.props.form;
    let { remember, gasEth } = getFieldsValue()
    let { recommendedGasEth } = this.state
    let gasEthForShow = gasEth || recommendedGasEth[1]
    let ethAvail = this.getEthAvail()

    const formItemLayout = {
      labelCol: { span: 5 },
      wrapperCol: { span: 19 },
    };
    const noLabelLayout = {
      wrapperCol: { span: 19, offset: 5 },
    };

    return (
      <Modal 
        // bodyStyle={{backgroundColor: '#363636', color: '#d0d0d0'}}
        title={intl.formatMessage({id: `modal.upsertPwd.title.${type}`})}
        visible={this.state.visible}
        maskClosable={true}
        onOk={() => {

        }}
        onCancel={() => {
          this.setState({visible: false})
          this.props.onFail()
          this.props.onDestory()
        }}
        footer={null}
        width={550}
        className="password-modal"
      >
        <Box pt={20} pb={30} justifyContent="center">
          <Box width={440} mx="auto">
            <Form className="upsert-password-form" onSubmit={(e) => {
              utils.withLoading(() => this.submit(e))
            }}>
              <Box>
                <Form.Item {...formItemLayout} label={intl.formatMessage({id: `modal.upsertPwd.label.pwd.${type}`})} hasFeedback={false}>
                  {getFieldDecorator('password', {
                    rules: [
                      { required: true, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.required'}) },
                      { pattern: /[A-Z]+/, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.upperCase'}) },
                      { pattern: /[a-z]+/, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.lowerCase'}) },
                      { pattern: /[0-9]+/, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.number'}) },
                      { min: 8, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.min'}) },
                    ],
                  })(
                    <Input.Password size="large" onChange={() => this.checkInput()} />
                  )}
                </Form.Item>
              </Box>
              <Box mt={20}>
                <Form.Item {...formItemLayout} label={intl.formatMessage({id: `modal.upsertPwd.label.confirmPwd.${type}`})}>
                  {getFieldDecorator('confirmPassword', {
                    rules: [
                      { required: true, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.required'}) },
                      {
                        validator: (rule, value, callback) => {
                          const { form } = this.props;
                          if (value && value !== form.getFieldValue('password')) {
                            callback(intl.formatMessage({id: 'modal.upsertPwd.errorInfo.pwdSame'}));
                          } else {
                            callback();
                          }
                        }
                      }
                    ],
                  })(
                    <Input.Password size="large" onChange={() => this.checkInput()} />
                  )}
                </Form.Item>
              </Box>
              <Box mt={20}>
                <Form.Item {...noLabelLayout}>
                  <FormErrorInfo errors={this.state.errors} items={this.errorInfos} />
                </Form.Item>
              </Box>
              {/* {renderIf(type === 'create')(
                <Box mt={20}>
                  <Form.Item {...formItemLayout} label={intl.formatMessage({id: `modal.upsertPwd.label.registerFee`})}>
                    <Input size="large" disabled value={`0.005 ETH  $${utils.asLegalMoney(0.005, 'ETH', 'USD')}`} />
                  </Form.Item>
                </Box>
              )} */}
              {/* <Box mt={20}>
                <Form.Item {...formItemLayout} label={intl.formatMessage({id: `modal.upsertPwd.label.minerFee`})}>
                  <Input size="large" disabled value={`${gasEthForShow} ETH  $${utils.asLegalMoney(gasEthForShow, 'ETH', 'USD')}`} />
                </Form.Item>
              </Box>
              {renderIf(gasEth >= ethAvail)(
                <Box mt={15}>
                  <Form.Item {...noLabelLayout}>
                    <Text color="#F0505A" lineHeight="14px">{intl.formatMessage({id: 'modal.upsertPwd.hint.minerFeeMax'}, { ethAvail })}</Text>
                  </Form.Item>
                </Box>
              )}
              <Form.Item {...noLabelLayout}>
                {getFieldDecorator('gasEth', {
                  initialValue: recommendedGasEth[1]
                })(
                  <Slider size="small" min={recommendedGasEth[0]} max={recommendedGasEth[2]} marks={{
                    [recommendedGasEth[0]]: '',
                    // [recommendedGasEth[1]]: '',
                    [recommendedGasEth[2]]: '',
                }} step={0.000001} />
                )}
              </Form.Item> */}
              {/* <Form.Item {...noLabelLayout}>
                <Flex mt={10} justifyContent="space-between">
                  {getFieldDecorator('remember', {
                    initialValue: false
                  })(
                    <Checkbox style={{color: '#1890ff', fontSize: '14px'}}></Checkbox>
                  )}
                </Flex>
                { remember && (<Text lineHeight={'30px'} fontSize={14} color="#969696" ml={'6px'}>24 </Text>) }
              </Form.Item> */}
              <Box mt={20}>
                <Form.Item {...noLabelLayout}>
                  <Button size="large" style={{width: '100%'}} type="primary" htmlType="submit" >
                    <FormattedMessage id="confirm" />
                  </Button>
                </Form.Item>
              </Box>
              {
                renderIf(type === 'create')(
                  <Box>
                    <Text mt={30} color={"#969696"} fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.upsertPwd.create.minerFee.hint1'})}</Text>
                    <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.upsertPwd.create.minerFee.hint2'})}</Text>
                  </Box>
                )
              }
              {
                renderIf(type === 'modify')(
                  <Box>
                    <Text mt={30} color={"#969696"} fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.upsertPwd.modify.minerFee.hint1'})}</Text>
                    <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.upsertPwd.modify.minerFee.hint2'})}</Text>
                  </Box>
                )
              }
            </Form>
          </Box>
        </Box>
      </Modal>
    )
  }
}

const WrappedForm = Form.create({ name: 'upsert_password_form' })(Widget);

const mapStateToProps = ({ metaMask, dexAccount, rate, walletBalance }) => {
  return {
    metaMask,
    dexAccount,
    rate,
    walletBalance
  };
};
const mapDispatchToProps = ({ dexAccount, walletBalance }) => {
  return {
    updateAccount: dexAccount.updateAccount,
    checkAccountCreated: dexAccount.checkAccountCreated,
    fetchWalletBalances: walletBalance.fetchAllBalances,
  };
};
let connectedWidget = connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(WrappedForm)

export default function promisefy (props) {
  console.info({props})
  return new Promise((resolve, reject) => {
    funcfy(connectedWidget, {
      ...props,
      onSucess: resolve,
      onFail: reject,
    })
  })
}