import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Divider, Table } from 'antd';
import { Account } from 'librarys/lightcone_v3'

@injectIntl
class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }

  render() {
    let { dexAccount, metaMask, intl } = this.props
    let { installed, enable, startConnecting } = metaMask
    let { address, accountId, apiKey, passwordHash } = dexAccount

    if (startConnecting || !installed || !enable || !address || !passwordHash) return <></>

    // TODO: window.wallet( initAccountFromMetaMask window.wallet)
    // if (!window.wallet) {
    //   window.wallet = ethereum.account.fromMetaMask(
    //     window.web3,
    //     address,
    //     address
    //   )
    // }
    let exchangeAccount = new Account(window.wallet)
    const keyPair = exchangeAccount.generateKeyPair(passwordHash)

    let infos = [
      { label: intl.formatMessage({id: 'mine.apikey.label.accountId'}), value: accountId },
      { label: intl.formatMessage({id: 'mine.apikey.label.addr'}), value: address },
      { label: intl.formatMessage({id: 'mine.apikey.label.apiSk'}), value: apiKey },
      { label: intl.formatMessage({id: 'mine.apikey.label.pkX'}), value: keyPair.publicKeyX },
      { label: intl.formatMessage({id: 'mine.apikey.label.pkY'}), value: keyPair.publicKeyY },
      { label: intl.formatMessage({id: 'mine.apikey.label.sk'}), value: keyPair.secretKey },
    ]

    return (
      <Box {...this.props}>
        <Text mb={'14px'} color="#323232" fontSize={16} fontWeight="bold">API KEY</Text>
        <Divider/>
        <Box>
        {
          infos.map(info => (
            <Flex key={info.label} height={50}>
              <Text fontSize={14} width={100} color="#969696">{info.label}:</Text>
              <Text fontSize={14} color="#323232">{info.value}</Text>
            </Flex>
          ))
        }
        </Box>
      </Box>
    )
  }
}

const mapStateToProps = ({ dexAccount, metaMask }) => {
  return {
    dexAccount,
    metaMask
  };
};

const mapDispatchToProps = ({ metaMask }) => {
  return {
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))