import _ from 'lodash'
import { Account, config, exchange } from 'librarys/lightcone_v3';
import utils, { notification } from 'utils'
import api from 'api'
const BigNumber = require('bignumber.js');

const initState = {
  buyPrice: null,
  buyAmount: null,
  buyTotal: null,

  sellPrice: null,
  sellAmount: null,
  sellTotal: null,

  // 
  marketBuyTotal: null,
  // 
  marketSellAmount: null,
}

async function placeAnOrder (dispatch, store, isBuy, tokenS, tokenB, amountS, amountB) {
  let { tokens, dexAccount, persistData } = store
  let { channelName } = persistData

  let owner = window.wallet.account
  let { accountId, passwordHash, apiKey } = dexAccount

  let exchangeAccount = new Account(window.wallet);
  const keyPair = exchangeAccount.generateKeyPair(passwordHash);
  let { publicKeyX, publicKeyY, secretKey: tradingPrivKey } = keyPair

  let tokenSConfig = _.find(tokens.list, token => token.symbol.toLowerCase() === tokenS.toLowerCase())
  const orderId = await api.getOrderId(accountId, tokenSConfig.relayTokenId, apiKey);
  
  // Timestamp in second
  const validSince = new Date().getTime() / 1000 - 3600;
  const validUntil = new Date().getTime() / 1000 + 3600 * 24 * 30;

  console.info(`:`, {
    owner,
    accountId,
    // keyPair: {
    //   publicKeyX,
    //   publicKeyY,
    //   secretKey: tradingPrivKey,
    // },
    tokenS,
    tokenB,
    amountS,
    amountB,
    orderId,
    validSince: Math.floor(validSince),
    validUntil: Math.floor(validUntil),
    buy: isBuy
  })

  const signedEtheruemTx = exchange.submitOrder(window.wallet, {
    owner,
    accountId,
    keyPair: {
      publicKeyX,
      publicKeyY,
      secretKey: tradingPrivKey,
    },
    tokenS,
    tokenB,
    amountS,
    amountB,
    orderId,
    validSince: Math.floor(validSince),
    validUntil: Math.floor(validUntil),
    buy: isBuy
  })

  if (channelName) {
    signedEtheruemTx.channelId = `wedex::subchannel::${channelName}`
  }

  console.log('Order signed tx: ', signedEtheruemTx);
  const response = await api.submitOrderToLightcone(signedEtheruemTx, apiKey);
  console.log('Response of submitOrder: ', response);

  if (!response) return
  dispatch.appState.update({
    notification: {
      type: 'success',
      message: 'notify.placeOrder.success'
    },
  })
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    initialize: (state, payload) => initState,
  },
  effects: dispatch => ({
    async placeBuyOrder (payload, store) {
      let { markets, placeOrder } = store
      let { baseToken: tokenB, quoteToken: tokenS } = markets.currentMarket
      let { buyAmount, buyPrice } = placeOrder
      if (!buyAmount || !buyPrice) return

      buyAmount = new BigNumber(buyAmount);
      let amountS = buyAmount.times(buyPrice).toFixed();
      let amountB = buyAmount.toFixed();

      let result = utils.withLoading(() => placeAnOrder(dispatch, store, true, tokenS, tokenB, amountS, amountB))
      return result
    },
    async placeSellOrder (payload, store) {
      let { markets, placeOrder } = store
      let { baseToken: tokenS, quoteToken: tokenB } = markets.currentMarket
      let { sellAmount, sellPrice } = placeOrder
      if (!sellAmount || !sellPrice) return

      sellAmount = new BigNumber(sellAmount);
      let amountB = sellAmount.times(sellPrice).toFixed();
      let amountS = sellAmount.toFixed();

      let result = utils.withLoading(() => placeAnOrder(dispatch, store, false, tokenS, tokenB, amountS, amountB))
      return result
    },
  })
}