import numeral from 'numeral';
import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Pagination } from 'components';
import { Table, Select, DatePicker, Divider } from 'antd';
import utils from 'utils'
import api from 'api'
import { ENUM_ORDER_STATUS } from 'utils/enum'
const { Option } = Select;
const { RangePicker } = DatePicker;

@injectIntl
class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      list: [],
      filters: {
        market: undefined,
        start: undefined,
        end: undefined,
        side: undefined,
        status: undefined
      }
    }
  }

  componentDidMount() {
  }

  fetchData = async function (pageParams) {
    let { dexAccount: { accountId, apiKey } } = this.props
    let { filters: { market, side, start, end, status } } = this.state
    let { offset, limit } = utils.formatPageParams(pageParams)

    let resp = await api.getOrders({
      accountId,
      apiKey,
      offset,
      limit,
      market,
      side,
      start,
      end,
      statuses: status ? [status] : undefined
    })
    let { totalNum, orders } = resp || {}

    orders = orders || []
    return {
      // TODO: 
      count: totalNum || 0,
      rows: orders
    }
  }.bind(this)
  
  onDataFetched = function (list) {
    console.info({list})
    this.setState({list})
  }.bind(this)

  renderFilters () {
    let { markets: marketsStore, intl } = this.props
    let markets = marketsStore.markets.map(item => item.market)
    return (
      <Flex justifyContent="flex-end" flex={1}>
        <Select
          placeholder={intl.formatMessage({id: 'filters.allPair'})}
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (market) => {
            console.info({market})
            await this.setState(({ filters }) => {
              filters.market = market
              return { filters }
            })
            this.pagination.reset()
          }}
        >
          {
            markets.map(market => (
              <Option key={market} value={market}>{market}</Option>
            ))
          }
        </Select>
        <Select
          placeholder={intl.formatMessage({id: 'filters.allType'})}
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (side) => {
            console.info({side})
            await this.setState(({ filters }) => {
              filters.side = side
              return { filters }
            })
            this.pagination.reset()
          }}
        >
          <Option value={'BUY'}><FormattedMessage id="buy" /></Option>
          <Option value={'SELL'}><FormattedMessage id="sell" /></Option>
        </Select>
        <Select
          placeholder={intl.formatMessage({id: 'filters.allStatus'})}
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (status) => {
            console.info({status})
            await this.setState(({ filters }) => {
              filters.status = status
              return { filters }
            })
            this.pagination.reset()
          }}
        >
          {
            _.map(ENUM_ORDER_STATUS.getOptions(), status => (
              <Option key={status.value} value={status.value}>
                <FormattedMessage id={`enum.orderStatus.${status.value}`} />
              </Option>
            ))
          }
        </Select>
        <RangePicker placeholder={[
          intl.formatMessage({id: 'startDate'}),
          intl.formatMessage({id: 'endDate'})
        ]}
        onChange={async range => {
          let [start, end] = range
          start = start && start.startOf('day').toDate().valueOf()
          end = end && end.endOf('day').toDate().valueOf()

          console.info({start, end})
          await this.setState(({ filters }) => {
            filters.start = start
            filters.end = end
            return { filters }
          })
          this.pagination.reset()
        }} />
      </Flex>
    )
  }

  render() {
    let { list } = this.state
    let { intl } = this.props

    const columns = [
      {
        align: 'left',
        title: intl.formatMessage({id: 'timestamp'}),
        dataIndex: 'createdAt',
        width: 90,
        render: (unused, row) => {
          return (<Text>{moment(row.createdAt).format('MM-DD HH:mm')}</Text>)
        }
      },
      {
        align: 'left',
        title: intl.formatMessage({id: 'exchangePair'}),
        dataIndex: 'market',
        width: 80,
        render: (market, row) => {
          let [baseSymbol, quoteSymbol] = market.split('-')
          return (<Flex>{baseSymbol}<Text color="hint">/{quoteSymbol}</Text></Flex>)
        }
      },
      {
        align: 'left',
        title: intl.formatMessage({id: 'type'}),
        dataIndex: 'side',
        width: 30,
        render: (unused, row) => {
          return (<Text><FormattedMessage id={row.side === 'BUY' ? "buy" : "sell"} /></Text>)
        }
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'size'}),
        dataIndex: 'filledSize',
        width: 60,
        render: (filledSize, row) => {
          let isBuy = (row.side === 'BUY')
          return (
            <Text color={isBuy ? "success" : "failure"}>{isBuy ? '+' : '-'}{filledSize}</Text>
          )
        }
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'price'}),
        dataIndex: 'price',
        width: 100,
        render: (price, row) => (
          <Text>{price}</Text>
        )
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'mine.order.list.header.volume'}),
        dataIndex: 'volume',
        width: 150,
        render: (volume, row) => (
          <Flex alignItems="center" justifyContent="flex-end" flexWrap="no-wrap">
            <Text mr="5px">{volume}</Text>
            <Divider style={{position: 'unset', background: '#646478', margin: '0 5px'}} type="vertical" />
            <Text width={34} style={{ whiteSpace: 'nowrap'}}>{row.filled}</Text>
          </Flex>
        )
      },
      // {
      //   align: 'right',
      //   title: ``,
      //   dataIndex: 'filledSize',
      //   width: 180,
      //   render: (filledSize, row) => (
      //     <Flex alignItems="center" justifyContent="flex-end">
      //       <Text mr={'4px'}>{filledSize}</Text>
      //     </Flex>
      //   )
      // },
      {
        align: 'right',
        title: intl.formatMessage({id: 'fee'}),
        dataIndex: 'filledFee',
        width: 150,
        render: (filledFee, row) => {
          let [baseSymbol, quoteSymbol] = row.market.split('-')
          return (
            <Flex alignItems="center" justifyContent="flex-end">
              <Text mr={'4px'}>{filledFee} {row.side === 'BUY' ? baseSymbol : quoteSymbol }</Text>
            </Flex>
          )
        }
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'status'}),
        dataIndex: 'status',
        key: 'status',
        width: 80,
        render: (status, row) => (
          <Text><FormattedMessage id={`enum.orderStatus.${status}`} /></Text>
        )
      },
    ];

    return (
      <Box>
        { this.renderFilters() }
        <Table rowKey="hash" columns={columns} dataSource={list}
          className="mine-table"
          pagination={false}
          style={{ minHeight: '450px' }}
          // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
        />
        <Pagination ref={pagination => this.pagination = pagination} fetchData={this.fetchData} onFetched={this.onDataFetched}></Pagination>
      </Box>
    )
  }
}

const mapStateToProps = ({ dexAccount, markets }) => {
  return {
    dexAccount,
    markets
  };
};

const mapDispatchToProps = ({ order }) => {
  return {
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))