import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { injectIntl, FormattedMessage  } from 'react-intl'
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Table, Tooltip, Divider, Checkbox, Menu, Dropdown, Icon } from 'antd';
import { TabNav } from './Common'
import { TransferModal } from 'widgets/Dialog'
import { ENUM_ORDER_STATUS } from 'utils/enum'
import utils, { notification } from 'utils'
import { Spin } from 'components'

const BigNumber = require('bignumber.js');

@injectIntl
class MyOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tabValue: 'current',
      hideSmall: false,
    }
  }

  componentDidMount() {
  }

  onTabChange (tab) {
    //  tab 
    if (tab.value === 'current' || tab.value === 'history') {
      this.props.updateFilterCond({
        type: tab.value,
        page: 1,
        pageSize: 50
      })
      this.props.fetchOrders()
    } else if (tab.value === 'balance') {
      //  tab
      // 
      this.props.fetchWalletBalances()
    }
    this.setState({tabValue: tab.value})
  }

  onChangeShowAll (e) {
    //  onchange  tab 
    e.stopPropagation()
    this.props.updateFilterCond({
      allMarket: e.target.checked,
      page: 1,
      pageSize: 50
    })
    this.props.fetchOrders()
  }

  genOrderColumns () {
    let { intl, order, cancelAnOrders, fetchOrders } = this.props

    let columns = [
      {
        align: 'left',
        title: intl.formatMessage({id: 'timestamp'}),
        dataIndex: 'createdAt',
        width: 100,
        render: (unused, row) => {
          return (<Text>{moment(row.createdAt).format('MM-DD HH:mm')}</Text>)
        }
      },
      {
        align: 'left',
        title: intl.formatMessage({id: 'exchangePair'}),
        dataIndex: 'market',
        width: 80,
        render: (market, row) => {
          let [baseSymbol, quoteSymbol] = market.split('-')
          return (<Flex>{baseSymbol}<Text color="hint">/{quoteSymbol}</Text></Flex>)
        }
      },
      {
        align: 'center',
        title: intl.formatMessage({id: 'side'}),
        dataIndex: 'side',
        width: 30,
        render: (unused, row) => {
          return (
            <Text color={row.side === 'BUY' ? "success" : "failure"}>
              {/* 'BUY' */}
              <FormattedMessage id={_.toLower(row.side)}/>
            </Text>
          )
        }
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'price'}),
        dataIndex: 'price',
        width: 110,
        render: price => (
          <Text>{price}</Text>
        )
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'size'}),
        dataIndex: 'size',
        width: 110,
        render: size => (
          <Text>{size}</Text>
        )
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'myOrder.list.header.filledVolume'}),
        dataIndex: 'filledVolume',
        width: 110,
        render: filledVolume => (
          <Text>{filledVolume}</Text>
        )
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'myOrder.list.header.filledSize'}),
        dataIndex: 'filledSize',
        width: 135,
        render: (filledSize, row) => (
          <Flex alignItems="center" justifyContent="flex-end">
            <Text mr={'4px'}>{filledSize}</Text>
            <Divider style={{position: 'unset', background: '#646478', margin: '0 2px'}} type="vertical" />
            <Text width={34} >{row.filled}</Text>
          </Flex>
        )
      },
    ];

    if (order.cond.type === 'current') {
      columns.push({
        align: 'right',
        title: intl.formatMessage({id: 'operations'}),
        dataIndex: '',
        key: 'ops',
        width: 80,
        render: (unused, row) => (
          <Text color={row.filled === '100%' ? 'hint' : 'blue'} sx={{ cursor: row.filled !== '100%' && 'pointer' }} onClick={async () => {
            if (row.filled === '100%') return
            await cancelAnOrders(row)
            notification['success']({
              message: intl.formatMessage({id: 'myOrder.notify.cancelSuccess'}),
            })
            fetchOrders()
          }}>
            <FormattedMessage id="myOrder.operations.cancel" />
          </Text>
        )
      })
    } else {
      columns.push({
        align: 'right',
        title: ``,
        dataIndex: 'status',
        key: 'ops',
        width: 80,
        render: (status, row) => (
          <Text>
            <FormattedMessage id={`enum.orderStatus.${status}`} />
          </Text>
        )
      })
    }
    return columns
  }

  genBalanceColumns () {
    let { intl, order, cancelAnOrders, fetchOrders } = this.props

    let columns = [
      {
        title: intl.formatMessage({id: 'myOrder.list.header.coinType'}),
        dataIndex: 'tokenId',
        key: 'tokenId',
        width: 140,
        render: (unused, record) => {
          return (
            <Flex>
              <Text>{record.token.symbol}</Text>
              <Text color="#969696">({record.token.name})</Text>
            </Flex>
          )
        },
      },
      {
        title: intl.formatMessage({id: 'myOrder.list.header.wallet'}),
        dataIndex: 'walletAmount',
        key: 'walletAmount',
        width: 120,
        render: (walletAmount, record) => {
          return <Text>{utils.toTokenPrecision(walletAmount, record.token.symbol)}</Text>
        },
      },
      {
        title: intl.formatMessage({id: 'myOrder.list.header.availAmount'}),
        dataIndex: 'availAmount',
        key: 'availAmount',
        width: 120,
        render: (availAmount, record) => {
          return <Text>{utils.toTokenPrecision(availAmount, record.token.symbol)}</Text>
        },
      },
      {
        title: intl.formatMessage({id: 'myOrder.list.header.amountLocked'}),
        dataIndex: 'amountLocked',
        key: 'amountLocked',
        width: 120,
        render: (amountLocked, record) => {
          return <Text>{utils.toTokenPrecision(amountLocked, record.token.symbol)}</Text>
        },
      },
      {
        title: intl.formatMessage({id: 'myOrder.list.header.total'}),
        dataIndex: 'total',
        key: 'total',
        width: 120,
        render: (total, record) => {
          return <Text>{utils.toTokenPrecision(total, record.token.symbol)}</Text>
        },
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'operations'}),
        dataIndex: '',
        key: 'ops',
        width: 120,
        render: (unused, record) => (
          <Flex alignItems="center" justifyContent="flex-end">
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={() => TransferModal({
                initialSymbol: record.symbol,
                initialType: 'deposit',
              })}
            >
              <FormattedMessage id="deposit" />
            </Text>
            <Divider type="vertical" style={{background: 'rgba(255,255,255,0.2)'}} />
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={() => TransferModal({
                initialSymbol: record.token.symbol,
                initialType: 'withdraw',
              })}
            >
              <FormattedMessage id="withdraw" />
            </Text>
          </Flex>
        )
      }
    ];
    return columns
  }

  genBalanceData () {
    let { balance, walletBalance, intl } = this.props
    let { hideSmall } = this.state

    let merged = balance.list.map(item => {
      let walletToken = _.find(walletBalance.list, b => b.symbol === item.token.symbol)
      item.walletAmount = walletToken ? walletToken.amount : 0
      item.total = item.walletAmount + item.totalAmount
      return item
    })

    if (hideSmall) {
      // 10usdt
      merged = _.reject(merged, item => {
        let usd = utils.asLegalMoney(Number(item.total), item.token.symbol)
        return usd < 10
      })
    }
    return merged
  }

  render() {
    let { intl, order, cancelAllOrders } = this.props
    let { tabValue } = this.state

    const tabs = [
      { text: intl.formatMessage({id: 'myOrder.tabs.current'}), value: 'current' },
      { text: intl.formatMessage({id: 'myOrder.tabs.history'}), value: 'history' },
      { text: intl.formatMessage({id: 'myOrder.tabs.balance'}), value: 'balance' },
    ]

    const filters = [
      { text: intl.formatMessage({id: 'myOrder.filters.all'}), value: 'ALL' },
      { text: intl.formatMessage({id: 'myOrder.filters.processed'}), value: 'PROCESSED' },
      { text: intl.formatMessage({id: 'myOrder.filters.cancelled'}), value: 'CANCELLED' },
    ]

    // 
    let columns = []
    if (tabValue !== 'balance') {
      columns = this.genOrderColumns()
    } else {
      columns = this.genBalanceColumns()
    }

    //  table 
    let tableData = tabValue === 'balance' ? this.genBalanceData() : order.list

    return (
      <Flex {...this.props} flexDirection="column">
        <TabNav tabs={tabs} onChange={tab => this.onTabChange(tab)}>
          <Box flex={1}></Box>
          {
            utils.renderIf(tabValue !== 'balance')(
              <Checkbox className="all-paire-type"
                checked={order.cond.allMarket}
                onChange={e => this.onChangeShowAll(e)}
              >
                <FormattedMessage id="myOrder.operations.showAll"/>
              </Checkbox>
            )
          }
          {
            utils.renderIf(tabValue !== 'balance' && order.cond.type === 'current')(
              <Text px={10} textAlign="right" color="blue" onClick={() => cancelAllOrders()}
                sx={{ cursor: 'pointer' }}
              >
                <FormattedMessage id="myOrder.operations.cancelLatest"/>
              </Text>
            )
          }
          {
            utils.renderIf(tabValue !== 'balance' && order.cond.type === 'history')(
              <Dropdown overlay={
                  <Menu onClick={({key: index}) => {
                    let historyStatus = filters[index].value
                    this.props.updateFilterCond({
                      historyStatus,
                      page: 1,
                      pageSize: 50
                    })
                    this.props.fetchOrders()
                  }}>
                  {
                    filters.map((menu, index) => (
                      <Menu.Item key={index}>
                        <Text fontSize={[0, 0, 0]} color='hint'>{ menu.text }</Text>
                      </Menu.Item>
                    ))
                  }
                  </Menu>
                }
                placement="bottomCenter" overlayClassName="nav-menu"
              >
                <Flex width={`100px`} px={10} justifyContent="flex-end" height={40} lineHeight={'40px'} alignItems="center">
                  {_.find(filters, {value: order.cond.historyStatus}).text}<Icon type="caret-down" />
                </Flex>
              </Dropdown>
            )
          }
          {
            utils.renderIf(tabValue === 'balance')(
              <Checkbox className="all-paire-type"
                checked={this.state.hideSmall}
                onChange={e => {
                  e.stopPropagation()
                  this.setState({hideSmall: e.target.checked})
                }}
              >
                <FormattedMessage id="mine.balance.operations.hideSmall"/>
              </Checkbox>
            )
          }
        </TabNav>
        <Flex bg="backgroundTint" px={15} flex={1}>
          <Spin spinning={order.loading}>
            <Table rowKey="hash" columns={columns} dataSource={tableData} align="right"
              style={{flex: 1}}
              // tableLayout="auto"
              className="exchange-table"
              pagination={false}
              scroll={{ y: this.props.height - 70 }}
              // height={this.props.height - 50}
              // // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
            />
          </Spin>
        </Flex>
      </Flex>
    )
  }
}

const mapStateToProps = ({ order, balance, walletBalance }) => {
  return {
    order,
    balance,
    walletBalance,
  };
};

const mapDispatchToProps = ({ order, walletBalance }) => {
  return {
    updateFilterCond: order.updateFilterCond,
    fetchOrders: order.fetchOrders,
    cancelAnOrders: order.cancelAnOrders,
    cancelAllOrders: () => utils.withLoading(() => order.cancelAllOrders()),
    fetchWalletBalances: walletBalance.fetchAllBalances,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyOrder);