import moment from 'moment'
import { push } from 'connected-react-router'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import api from 'api'
import utils, { notification } from 'utils'
import { ethereum } from 'librarys/lightcone_v3';

const initState = {
  address: '',
  accountId: '',
  passwordHash: '',
  // 
  apiKey: '',
  state: ENUM_ACCOUNT_STATUS.UNSIGNED.getValue(),
  rememberMeStartTime: null,
  rememberMeMinutes: null,
  canceledOrders: [],
}

export default {
  state: initState,
  reducers: {
    // 
    // TODO:  creating 
    switchAccount: (state, address) => {
      if (state.address === address) {
        return state
      } else {
        return {
          ...initState,
          address
        }
      }
    },
    clearAccountInfo ({ address }, payload) {
      return {
        ...initState,
        address
      }
    },
    updateAccount: (state, payload) => {
      return {
        ...state,
        ...payload
      }
    },
    checkAccountOvertime (state, payload) {
      let { rememberMeStartTime, rememberMeMinutes } = state
      if (rememberMeStartTime && rememberMeMinutes) {
        let validUntil = moment(rememberMeStartTime).add(rememberMeMinutes, 'minutes')
        if (moment().isAfter(validUntil)) {
          notification['success']({
            duration: 0,
            message: '',
            description: ''
          });
          return {
            ...initState,
            address: state.address,
            accountId: state.accountId,
          }
        }
      }
      return state
    }
  },
  effects: dispatch => ({
    async logout () {
      this.clearAccountInfo()
      // 
      dispatch(push('/'))
      //  socket  apikey socket 
      dispatch.socket.start()
      // 
      dispatch.order.initialize()
      dispatch.balance.initialize()
      dispatch.walletBalance.initialize()
      dispatch.placeOrder.initialize()
    },
    //  account 
    async getAccount (address, { dexAccount, socket }) {
      // initAccountFromMetaMask 
      // 
      if (dexAccount.address !== address) {
        this.switchAccount(address)
        // 
        dispatch(push('/'))
        //  socket  apikey socket 
        dispatch.socket.start()
      }

      const relayAccount = await api.lightconeGetAccount(address);
      // 
      if (!relayAccount) {
        this.updateAccount({ state: ENUM_ACCOUNT_STATUS.UNCREATED.getValue()})
        return
      } else {
        if (relayAccount.isFreeze) {
          // 
          this.updateAccount({ state: ENUM_ACCOUNT_STATUS.LOCKED.getValue() })
        } else {
          // 
          // 
          if (dexAccount.state === ENUM_ACCOUNT_STATUS.UNCREATED.getValue()) {
            this.updateAccount({ state: ENUM_ACCOUNT_STATUS.UNSIGNED.getValue() })
          }
        }
      }

      this.updateAccount({
        accountId: relayAccount.accountId,
      })
      dispatch.balance.startFetchBalance()
    },
    async loadAccount (payload, { dexAccount }) {
      // 
      if (!dexAccount.address) return

      //  window.wallet
      let { address } = dexAccount
      window.wallet = ethereum.account.fromMetaMask(window.web3, address, address)

      // 
      if (dexAccount.state === ENUM_ACCOUNT_STATUS.CREATING.getValue()) {
        dispatch.dexAccount.checkAccountCreated()
      } else {
        // account 
        // :  else  creating  uncreated 
        await dispatch.dexAccount.getAccount(address)
      }
    },
    async checkAccountCreated (payload, { dexAccount, appState }) {
      if (window.checkAccountInterval) {
        clearInterval(window.checkAccountInterval)
      }
      window.checkAccountInterval = setInterval(async () => {
        try {
          //  passwordHashapikey 

          const relayAccount = await api.lightconeGetAccount(window.wallet.account)
          // 1.  creating 
          if (!relayAccount) return
          //  isFreeze 
          if (relayAccount.isFreeze) return
          // accountId
          this.updateAccount({ accountId: relayAccount.accountId })

          // TODO
          // // 2.  updating 
          // //  
          // let valid = utils.checkPassword(dexAccount.passwordHash, relayAccount)
          // if (!valid) {
          //   // this.updateAccount({
          //   //   state: ENUM_ACCOUNT_STATUS.CREATED.getValue()
          //   // })
          //   return
          // }

          // account apiKey
          //  upsert 
          let apiKey = await utils.genApiKey(relayAccount.accountId, dexAccount.passwordHash)
          this.updateAccount({
            apiKey,
            state: ENUM_ACCOUNT_STATUS.LOGGED_IN.getValue()
          })
          dispatch.balance.startFetchBalance()
          dispatch.order.fetchOrders()

          dispatch.appState.update({
            notification: {
              type: 'success',
              message: 'notify.account.modify.effective'
            },
          })

          clearInterval(window.checkAccountInterval)
        } catch (e) {}
      }, 5000);
    }
  })
}