import _ from 'lodash'
import numeral from 'numeral'
import api from 'api';
import { Account } from 'librarys/lightcone_v3';
import { ENUM_ORDER_STATUS } from 'utils/enum'
import utils, { notification } from 'utils'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'

const initState = {
  loading: false,
  list: [],
  processingList: [],

  cond: {
    page: 1,
    pageSize: 50,
    // 
    type: 'current', // ['current', 'history']
    // [(ALL), (PROCESSED), (CANCELLED)]
    historyStatus: 'ALL',
    allMarket: false
  }
}

export default {
  state: initState,
  reducers: {
    initialize: (state, payload) => initState,
    update: (state, payload) => ({ ...state, ...payload }),
    updateFilterCond: (state, payload) => {
      let cond = { ...state.cond, ...payload }
      return {
        ...state,
        cond
      }
    },
  },
  effects: dispatch => ({
    async fetchProcessingOrders (payload, { dexAccount, markets: { currentMarket } }) {
      let { accountId, passwordHash, state, apiKey } = dexAccount
      let { market: marketPair, baseToken, quoteToken } = currentMarket
      let market = marketPair
      let statuses = [ENUM_ORDER_STATUS.PROCESSING.getValue()]
      let resp = await api.getOrders({accountId, apiKey, offset: 0, limit: 100, market, statuses})
      let { totalNum, orders } = resp || {}
      if (!orders) {
        // this.update({ loading: false })
        return
      }

      this.update({
        processingList: orders,
        // loading: false,
      })
    },
    async incrementalUpdateProcessing (order, { order: orderState, markets }) {
      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      let { processingList } = orderState
      let updatedOrders = utils.formatOrders([order])
      processingList = _.chain([...updatedOrders, ...processingList])
              .filter(o => o.status === 'processing')
              .unionBy(o => o.hash)
              .orderBy(['createdAt'], ['desc'])
              .value()

      this.update({
        processingList,
      })
    },
    async fetchOrders (payload, { order, dexAccount, markets: { currentMarket } }) {
      let { accountId, passwordHash, state, apiKey } = dexAccount
      if (!accountId || !ENUM_ACCOUNT_STATUS.LOGGED_IN.equals(state)) return

      if (!currentMarket) return
      let { market: marketPair, baseToken, quoteToken } = currentMarket
      if (!marketPair) return

      //  1.  order 
      dispatch.socket.unsubscribe([{
        topic: 'order',
        unsubscribeAll: true
      }])
      dispatch.socket.subscribe([{
        topic: 'order',
        market: marketPair,
      }])

      // 
      dispatch.order.fetchProcessingOrders()

      // 2. 
      this.update({
        list: [],
        loading: true,
      })

      let { offset, limit } = utils.formatPageParams(order.cond)
      let { type, allMarket, historyStatus } = order.cond

      //  market 
      let market
      if (allMarket) {
        market = undefined
      } else {
        market = marketPair
      }

      //  status 
      let statuses
      if (type === 'current') {
        statuses = [ENUM_ORDER_STATUS.PROCESSING.getValue()]
      } else {
        if (historyStatus === 'ALL') {
          statuses = [ENUM_ORDER_STATUS.CANCELLED.getValue(), ENUM_ORDER_STATUS.PROCESSED.getValue()]
        } else if (historyStatus === 'CANCELLED') {
          statuses = [ENUM_ORDER_STATUS.CANCELLED.getValue()]
        } else {
          statuses = [ENUM_ORDER_STATUS.PROCESSED.getValue()]
        }
      }

      let resp = await api.getOrders({accountId, apiKey, offset, limit, market, statuses});
      let { totalNum, orders } = resp || {}
      if (!orders) {
        this.update({ loading: false })
        return
      }

      this.update({
        list: orders,
        loading: false,
      })
    },
    async incrementalUpdate (order, { order: orderState, markets }) {
      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      let { list, cond } = orderState

      // 1. union
      // 2. 
      if (cond.type === 'history') return

      let updatedOrders = utils.formatOrders([order])
      list = _.chain([...updatedOrders, ...list]).unionBy(o => o.hash).orderBy(['createdAt'], ['desc']).value()

      this.update({
        list,
      })
    },
    async cancelAnOrders (order, { markets, dexAccount, placeOrder, order: orderStore }) {
      let { accountId, passwordHash, apiKey } = dexAccount
      let exchangeAccount = new Account(window.wallet);
      const keyPair = exchangeAccount.generateKeyPair(passwordHash);
      let { publicKeyX, publicKeyY, secretKey } = keyPair

      const signed = exchangeAccount.submitFlexCancel(
        accountId,
        publicKeyX,
        publicKeyY,
        secretKey,
        order.hash,
        order.clientOrderId
      );
      console.log('signed cancel order result: \n', signed);

      const response = await api.cancelOrders(
        signed.account.accountId,
        signed.orderHash,
        signed.clientOrderId,
        signed.signature,
        apiKey
      )
      console.log('Response of submitOrder: ', response);

      if (!response) return
      // notification['success']({
      //   duration: 5,
      //   message: ''
      // })

      dispatch.order.fetchOrders()
      // TODO: test
      // dispatch.balance.fetchBalances()

      return response
    },
    async cancelAllOrders (payload, { markets, dexAccount, placeOrder, order: orderStore }) {
      let orders = _.chain(orderStore.list)
                    .filter({ status: ENUM_ORDER_STATUS.PROCESSING.getValue() })
                    .slice(0, 10)
                    .value()
      let promises = _.map(orders, order => dispatch.order.cancelAnOrders(order))
      let resp = await Promise.all(promises)
      dispatch.appState.update({
        notification: {
          type: 'success',
          message: 'notify.order.cancel.success'
        },
      })

      dispatch.order.fetchOrders()
      // TODO: test
      // dispatch.balance.fetchBalances()
    }
  })
}
