import React from 'react';
import { Route, Switch, Redirect } from 'react-router';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import { Icon, ConfigProvider } from 'antd';
import {
  Text,
  Box
} from 'rebass'
// import NavBarLevel1 from 'components/nav-bar/NavBarLevel1';
import TopNav from 'widgets/TopNav';
import Home from 'views/Home';
import Mine from 'views/mine/Mine';
import ActivityList from 'views/ActivityList';
import Activity from 'views/Activity';
import { notification } from 'utils'

const mapState = ({ persistData, appState }) => {
  return {
    persistData,
    notification: appState.notification
  }
}
const mapDispatch = ({ }) => {
  return {
  }
}

@connect(mapState, mapDispatch)
@injectIntl
export default class NumericInput extends React.Component {

  componentWillReceiveProps(newProps) {
    let { intl } = this.props
    if (
      // 
      // // newProps.notification.message !== this.props.notification.message && 
      // newProps.notification.message &&
      // //  notification 
      // newProps.ready === this.props.ready &&
      // newProps.loading === this.props.loading &&
      // newProps.selectedTab === this.props.selectedTab &&
      // newProps.showMarketDrawer === this.props.showMarketDrawer

      //  message notification 
      newProps.notification !== this.props.notification
    ) {
      let { type, message, ...otherProps } = newProps.notification
      // 
      // https://github.com/formatjs/react-intl/blob/master/docs/API.md#createintl
      notification[type]({
        message: intl.formatMessage({id: message}),
        ...otherProps
      })
    }
  }

  render() {
    let { intl } = this.props

    const customizeRenderEmpty = () => (
      <Box mt={20} sx={{ textAlign: 'center' }}>
        <Icon type="exception" style={{ fontSize: 35 }} />
        <Text mt={'5px'}>{intl.formatMessage({id: 'noData'})}</Text>
      </Box>
    )

    let { locale, market } = this.props.persistData
    return (
      <ConfigProvider renderEmpty={customizeRenderEmpty}>
        {/* <NavBarLevel1 /> */}
        <TopNav />
        {/* Routes must be also defined in server.js */}
        {/* basename  Link  */}
        <Switch basename={locale}>
          <Route exact path="/" render={() => <Redirect to={`/cn/trade/${market}`} />} />
          <Route
            exact
            path="/trade"
            render={() => <Redirect to={`/cn/trade/${market}`} />}
          />
          <Route exact path="/:locale/trade/:market" component={Home} />
          <Route exact path="/:locale/mine/:tab" component={Mine} />
          <Route exact path="/:locale/activities" component={ActivityList} />
          <Route exact path="/:locale/activities/:id" component={Activity} />
        </Switch>
      </ConfigProvider>

    )
  }
}
