import moment from 'moment';
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Pagination, Spin } from 'components';
import { Table, Carousel } from 'antd';
import utils from 'utils'
import api from 'api'
import { ENUM_LANGUAGE_TYPE } from 'utils/enum'

const mapStateToProps = ({ persistData, appState }) => {
  return { persistData, appState }
};
const mapDispatchToProps = ({ depth, persistData }) => {
  return {
  }
};

@withRouter
@injectIntl
@connect(mapStateToProps, mapDispatchToProps)
export default class ActivityList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      recommended: [],
      list: []
    }
  }

  componentDidMount() {
    this.fetchRecommendedData()
  }

  fetchRecommendedData = async function () {
    let { persistData: { channelName, locale } } = this.props
    let lang = ENUM_LANGUAGE_TYPE[locale].getValue()
    let resp = await api.getArticleListRecommended({
      channelName,
      lang,
    })
    this.setState({ recommended: resp.data.rows })
  }

  fetchData = async function (pageParams) {
    this.setState({ loading: true })
    let { persistData: { channelName, locale } } = this.props
    let lang = ENUM_LANGUAGE_TYPE[locale].getValue()
    let resp = await api.getArticleList({
      channelName,
      lang,
      ...pageParams
    })
    this.setState({ loading: false })
    return resp.data
  }.bind(this)
  
  onDataFetched = function (list) {
    this.setState({list})
  }.bind(this)

  render() {
    let { recommended, list } = this.state
    let { history, persistData: { locale } } = this.props

    const statusMapping = {
      initiate: { bg: 'rgba(230,230,230,1)', color: '#969696' },
      underway: { bg: 'rgba(255,190,0,1)', color: '#fff' },
      finished: { bg: 'rgba(230,230,230,1)', color: '#969696' },
    }

    return (
      <Flex bg="white" fontSize={14} sx={{
        minHeight: `calc(100vh - ${50 + 3}px)`,
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'center'
      }}>
        <Carousel autoplay={true} style={{height: 422}}>
        {
          _.map(recommended, item => {
            let { poster } = item
            return (
              <Box key={item.id} sx={{
                height: 422,
                backgroundImage: `url(${poster})`,
                backgroundSize: 'cover',
                backgroundPosition: 'center',
              }} onClick={() => {
                history.push(`/${locale}/activities/${item.id}`);
              }}>
              </Box>
            )
          })
        }
        </Carousel>
        <Spin spinning={this.state.loading} color="#0064FF">
          <Flex mb={40} alignItems='center' flexDirection="column">
            <Flex mt={40} mb={40} maxWidth={1260} justifyContent="flex-start" sx={{
              flexWrap: 'wrap'
            }}>
            {
              _.map(list, item => {
                let { title, poster, startDate, endDate, abstract } = item
                let status = 'initiate'
                let now = moment()
                if (now.isAfter(moment(endDate).endOf('day'))) {
                  status = 'finished'
                } else if (now.isAfter(moment(startDate).startOf('day'))) {
                  status = 'underway'
                }
                let statusInfo = statusMapping[status]
                return (
                  <Box key={item.id} mx={20} mb={40} sx={{
                    width: 380,
                    border: '1px solid rgba(0,0,0,0.1)',
                    cursor: 'pointer'
                  }} onClick={() => {
                    history.push(`/${locale}/activities/${item.id}`);
                  }}>
                    <Flex sx={{
                      width: '100%',
                      height: 210,
                      flexDirection: 'column',
                      justifyContent: 'flex-start',
                      backgroundImage: `url(${poster})`,
                      backgroundSize: 'cover',
                      backgroundPosition: 'center',
                    }}>
                      <Box px={'15px'} py={'4px'} mt={'8px'} bg={statusInfo.bg} color={statusInfo.color} sx={{
                        borderRadius: '0px 20px 20px 0px',
                        width: 'fit-content',
                        fontWeight: 'bolder'
                      }}>
                        {statusInfo.text}
                        <FormattedMessage id={`activity.status.${status}`}></FormattedMessage>
                      </Box>
                      {/* <Text mt={'100px'} width="100%" textAlign="center">{startDate}-{endDate}</Text> */}
                    </Flex>
                    <Text mt={10} mx={15} fontSize={16} color="#323232" fontWeight="bolder" className="text-ellipsis">{title}</Text>
                    <Text mt={'4px'} mb={'6px'} mx={15} fontSize={12} color="#969696" className="text-ellipsis">{abstract}</Text>
                  </Box>
                )
              })
            }
            </Flex>
            <Pagination autoload={true} ref={pagination => this.pagination = pagination} fetchData={this.fetchData} onFetched={this.onDataFetched}></Pagination>
          </Flex>
        </Spin>
      </Flex>
    )
  }
}
