import _ from 'lodash';
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Table, Dropdown, Menu, Icon } from 'antd'
import utils from 'utils'
import { Svg } from 'components'
import Chart from 'bizcharts/lib/components/Chart';
import Geom from 'bizcharts/lib/components/Geom';
import Axis from 'bizcharts/lib/components/Axis';
import Tooltip from 'bizcharts/lib/components/Tooltip';
import Legend from 'bizcharts/lib/components/Legend';

const mapStateToProps = ({ markets, depth, trade, rate }) => {
  return {
    markets,
    depth,
  };
};

const mapDispatchToProps = ({ depth, placeOrder }) => {
  return {
  };
};

@injectIntl
@connect(mapStateToProps, mapDispatchToProps)
export class DepthChartContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  render() {
    let { depth, intl } = this.props
    let { asks, bids } = depth
    let data = [ ...asks, ...bids ]
    if (data.length === 0) return <></>

    data = _.chain(data)
      .reverse(data)
      // .map(item => {
      //   item.price = Number(item.price)
      //   return item
      // })
      .value()
    // let min = Number(data[0].price)
    // let max = Number(data[data.length - 1].price)
    return (
      <Box height={this.props.height} >
        <Chart height={this.props.height} data={data} forceFit scale={{
          price: {
            type: 'cat',
            // type: 'linear',
            // https://bizcharts.net/product/bizcharts/category/7/page/35#linear
            // linear
            // min,
            // max,
            // ticks: _.range(min, max, (max - min) / 8)
            // tickCount 8 
            alias: intl.formatMessage({id: 'depth.chart.geom.price'}),
            tickCount: 8
          },
          sumVolume: {
            // type: 'linear',
            alias: intl.formatMessage({id: 'depth.chart.geom.sum'})
          }
        }}>
          <Axis name="price"
            label={{
              formatter: val => {
                // return (val / 1000).toFixed(3) + "k";
                return Number(val).toFixed(8)
              }
            }}
          />
          <Axis name="sumVolume" grid={null}
            // position='right'
            label={{
              formatter: val => {
                // return (val / 1000).toFixed(3) + "k";
                return val
              }
            }}
          />
          {/* <Legend /> */}
          <Tooltip showTitle={false} />
          <Geom type="area" position="price*sumVolume"
            color={['type', ['rgba(80, 190, 140, 0.3)', 'rgba(240, 80, 90, 0.3)']]}
            tooltip={['price', (price)=>{ // array
              return {
                name: intl.formatMessage({id: 'depth.chart.geom.price'}),
                value: price
              }
            }]}
          />
          <Geom type="line" position="price*sumVolume" size={2}
            color={['type', ['rgba(80, 190, 140, 1)', 'rgba(240, 80, 90, 1)']]}
            tooltip={['sumVolume', (sumVolume)=>{ // array
              return {
                name: intl.formatMessage({id: 'depth.chart.geom.sum'}),
                value: sumVolume
              }
            }]}
          />
        </Chart>
      </Box>
    )
  }
}

