import _ from 'lodash'
import api from 'api'
import utils from 'utils'

const initState = {
  markets: [],
  stars: [],
  tabs: [],
  currentTab: null,
  currentMarkets: [],
  currentMarket: null,
  cond: {
    sort: 'market',
    sortDirection: 'asc'
  }
}

function formatTicker (ticker) {
  let { market: marketName, open, close, size: size24h, volume: volume24h } = ticker

  const [baseTokenSymbol, quoteTokenSymbol] = marketName.split('-')
  size24h = utils.fromWEI(baseTokenSymbol, size24h)
  volume24h = utils.fromWEI(quoteTokenSymbol, volume24h)

  open = Number(open)
  close = Number(close)
  let differ = close - open
  let variation = open ? differ / open : 0
  return { ...ticker, differ, close, variation, size24h, volume24h }
}

export default {
  state: initState,
  selectors: (slice, createSelector, hasProps) => ({
    getMarketBySymbol () {
      return (state, baseToken, quoteToken) => {
        let { markets } = state.markets
        return _.find(markets, market => (
          (market.baseToken === baseToken && market.quoteToken === quoteToken) ||
          (market.baseToken === quoteToken && market.quoteToken === baseToken)
        )) || {}
      }
    }
  }),
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    updateFilterCond: (state, payload) => {
      let {cond, currentMarkets} = state
      cond = { ...cond, ...payload }
      currentMarkets = _.orderBy(currentMarkets, [cond.sort], [cond.sortDirection])
      return {
        ...state,
        cond,
        currentMarkets
      }
    },
    updateMarketsTicker: (state, tickers) => {
      let { currentMarkets, currentMarket, cond } = state
      // 
      // 
      currentMarkets = currentMarkets.map(item => {
        let ticker = _.find(tickers, {market: item.market})
        if (!ticker) {
          return item
        }

        let { differ, close, variation, size24h, volume24h } = ticker
        let { pricePrecision } = item
        item.differ = differ.toFixed(pricePrecision)
        item.close = close.toFixed(pricePrecision)
        item.variation = variation
        item.size24h = size24h
        item.volume24h = volume24h
        return item
      })
      currentMarkets = _.orderBy(currentMarkets, [cond.sort], [cond.sortDirection])

      // 
      if (currentMarket) {
        let currentMatched = _.find(tickers, {market: currentMarket.market})
        // current 
        if (currentMatched) {
          let { close, variation } = currentMatched
          let { pricePrecision } = currentMarket
          currentMarket.close = close.toFixed(pricePrecision)
          currentMarket.variation = variation
        }
      }

      return {
        ...state,
        currentMarkets,
        currentMarket,
      }
    }
  },
  effects: dispatch => ({
    async fetchMarkets (payload, { router, persistData }) {
      //  'LRC-ETH'
      // let markets = await config.getMarkets()
      let { channelName, market } = persistData
      let markets = await api.getExchanges({ channelName })
      if (!markets) return

      let [ defaultBaseSymbol, defaultQuoteSymbol ] = market.split('-')
      let quoteTokens = _.chain(markets).map(market => market.quoteToken).uniq().value()
      let defaultTab = defaultQuoteSymbol
      let tabs = ['selfChoice', ...quoteTokens]
      this.update({
        markets,
        tabs,
      })
      
      //  tab
      dispatch.markets.setCurrentTab(defaultTab)
      
      // 
      let currentMarkets = _.filter(markets, {quoteToken: defaultTab})
      let currentMarket = _.find(currentMarkets, { baseToken: defaultBaseSymbol })
      if (currentMarket) {
        dispatch.markets.setCurrentMarket(currentMarket)
      }
    },
    async setCurrentTab (tab, { markets }) {
      let { cond } = markets

      let currentMarkets = []
      if (tab === 'selfChoice') {
        currentMarkets = markets.stars
      } else {
        currentMarkets = _.filter(markets.markets, {quoteToken: tab})
      }
      currentMarkets = _.orderBy(currentMarkets, [cond.sort], [cond.sortDirection])

      this.update({
        currentTab: tab,
        currentMarkets,
      })

      dispatch.markets.fetchCurrentMarkets()

      //  setCurrentMarket
      // let currentMarket = currentMarkets[0]
      // if (currentMarket) {
      //   dispatch.markets.setCurrentMarket(currentMarket)
      // }
    },

    async startFetchMarketInfo (payload, { markets }) {
      let { currentMarket: { market } } = markets
      console.info(`====== ${market} ======`)

      dispatch.depth.setLevel(0)
      dispatch.trade.fetchTrade()
      dispatch.order.fetchOrders()
      dispatch.ticker.fetchTicker()
      // dispatch.socket.subscribe([
      //   // `candlestick&${market}&1Hour`,
      //   `depth&${market}&1`,
      //   `trade&${market}`,
      //   `ticker&${market}`
      // ])
    },
    async setCurrentMarket (currentMarket, { markets, socket }) {
      this.update({
        currentMarket,
      })
      dispatch.placeOrder.initialize()
      dispatch.persistData.update({ market: currentMarket.market })

      if (!socket.ws || socket.ws.readyState !== 1) {
        if (socket.ws && socket.ws.readyState === 3) {
          // ( depth,trade,orders )
          // () 
          // socket 

          // TODO : socket
          // notification['success']({
          //   duration: 0,
          //   message: 'socket ',
          //   description: ''
          // })
          location.reload()
        }
        return
      }
      dispatch.markets.startFetchMarketInfo()
    },
    async onSocketReady (payload, { markets, depth }) {
      if (!markets.currentMarket) return
      dispatch.markets.startFetchMarketInfo()
    },

    async starMarket (currentMarket, { markets, depth }) {
      let stars = _.xorBy([currentMarket], markets.stars, 'market')
      this.update({
        stars,
      })
      // tab, 
      if (markets.currentTab === 'selfChoice') {
        dispatch.markets.setCurrentTab('selfChoice')
      }
    },
    async fetchCurrentMarkets (payload, { markets, dexAccount }) {
      let { apiKey } = dexAccount
      let { currentMarkets, currentMarket } = markets
      if (!currentMarkets) return

      let pairs = currentMarkets.map(item => item.market)
      //  currentMarket  currentMarkets currentMarket 
      //  tvchart  currentMarket ticker  socket 
      if (currentMarket) {
        pairs = _.uniq([...pairs, currentMarket.market])
      }
      if (!pairs.length) return

      // market  ticker
      // 
      let tickers = await api.getTickers({
        markets: pairs,
        apiKey
      })
      if (!tickers) return

      //  reducer tickerscurrentMarkets
      tickers = tickers.map(ticker => formatTicker(ticker))
      this.updateMarketsTicker(tickers)
    }
  })
}