import _ from 'lodash'
import moment from 'moment'
import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef } from 'react'
import { connect } from 'react-redux'
import { Modal, Form, Input, Select, Button, InputNumber, Slider } from 'antd'
import funcfy from './funcfyDialog'
import {
  Box,
  Flex,
  Text,
  Image
} from 'rebass'
import { injectIntl, useIntl } from 'react-intl';
import switchSideIcon from 'icons/switchSide.png'
import utils, { notification, renderIf } from 'utils'
import { config, common } from 'librarys/lightcone_v3';
import api from 'api'

// TODO:  gas eth  gasprice
//  gasprice 
const gasLimitDeposit = Number(config.getGasLimitByType("depositTo").gasInWEI)
const gasLimitWithdraw = Number(config.getGasLimitByType("withdrawFrom").gasInWEI)
const withdrawFeeInWEI = config.getFeeByType('withdraw').feeInWEI

const { Option } = Select;

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 },
};
const buttonItemLayout = {
  wrapperCol: { span: 20, offset: 4 },
};

const Widget = ({
  initialSymbol,
  initialType = 'deposit',
  onDestory,
  balance: balanceStore,
  walletBalance: walletBalanceStore,
  rate,
  persistData,
  fetchBalances,
  fetchWalletBalances,
  transfer
}) => {
  const intl = useIntl()
  const [visible, setVisible] = useState(false)
  const [symbol, setSymbol] = useState(initialSymbol)
  const [type, setType] = useState(initialType)
  const [transferAmount, setTransferAmount] = useState(0)
  const [recommendedGasPrice, setRecommendedGasPrice] = useState([0, 0, 0])
  // const [gasEth, setGasEth] = useState(0)

  let gasLimitInWEI = type === 'deposit' ? gasLimitDeposit : gasLimitWithdraw
  let gasEth = utils.fromWEI('ETH', recommendedGasPrice[1] * gasLimitInWEI)

  useEffect(() => {
    // fetchBalances()
    fetchWalletBalances()
    show()

    api.getRecommendedGasPrice().then(resp => {
      let [min, normal, max] = resp
      setRecommendedGasPrice([min, normal, max])
    })
  }, [])

  const show = async () => {
    setVisible(true)
  }

  const cancel = async () => {
    setVisible(false)
    onDestory()
  }

  const getAvailable = () => {
    let available = 0
    if (symbol) {
      if (type === 'deposit') {
        let balance = _.find(walletBalanceStore.list, b => b.symbol === symbol)
        available = balance ? balance.amount : 0
      } else {
        let balance = _.find(balanceStore.list, b => b.token.symbol === symbol)
        available = balance ? balance.availAmount : 0
      }
      available = Number(available)
      available = _.floor(available, 6)
    }
    return available
  }

  const getWalletEthAvail = () => {
    let ethBalance = _.find(walletBalanceStore.list, b => b.symbol === 'ETH')
    return ethBalance ? ethBalance.amount : 0
  }

  let balances = balanceStore.list
  let balancesSymbols = balances.map(b => b.token.symbol)
  let walletBalances = walletBalanceStore.list
  let walletBalancesSymbols = walletBalances.map(b => b.symbol)
  let symbolOptions = _.union(balancesSymbols, walletBalancesSymbols)

  //  symbol symbolOptions
  console.info({symbol})
  if (!symbol) {
    if (symbolOptions[0]) {
      setSymbol(symbolOptions[0])
    }
    return <></>
  }

  // getFeeByType
  // 
  // 
  // ETH 0
  let walletEthAvail = getWalletEthAvail()
  let withdrawFee = utils.fromWEI('ETH', withdrawFeeInWEI)
  
  // 
  let available = getAvailable()
  //  ETH 
  if (symbol === 'ETH' && type === 'deposit') {
    let ethAvailable = _.floor(available - gasEth, 6)
    available = _.max([ethAvailable, 0])
  }

  const submit = async (e) => {
    e.preventDefault()

    let { lastTransferTime } = persistData
    let duaration = moment().diff(moment(lastTransferTime), 'seconds')
    if (duaration < 30) {
      notification.info({message: intl.formatMessage({ id: `notify.transfer.duaration`}, { second: 30 - duaration })})
      return
    }

    if (type === 'withdraw') {
      if (walletEthAvail < withdrawFee) {
        return
      }
    } else if (type === 'deposit') {
      if (gasEth >= walletEthAvail) {
        return
      }
    }
    if (transferAmount <= 0) return

    if (transferAmount > available) {
      notification.info({ message: '' })
      return
    }

    console.info({type, symbol, transferAmount, gasEth})
    let result = await transfer(type, symbol, transferAmount, gasEth)
    if (result) {
      cancel()
    }
  }

  return (
    <Modal
      title={intl.formatMessage({id: type})}
      visible={visible}
      maskClosable={true}
      onCancel={() => cancel()}
      footer={null}
      width={550}
      className="password-modal"
    >
      <Box pt={30} pb={35} justifyContent="center">
        <Box width={440} mx="auto">
          <Form {...formItemLayout} onSubmit={submit}>
            <Form.Item label={intl.formatMessage({id: 'modal.transfer.label.coin'})}>
              <Select style={{color: '#323232'}} defaultValue={symbolOptions[0]} value={symbol} onChange={value => {
                setSymbol(value)
                setTransferAmount(0)
              }} size="large">
                { symbolOptions.map(symbol => (<Option value={symbol}>{symbol}</Option>)) }
              </Select>
            </Form.Item>
            <Box mt={28}>
              <Form.Item label={intl.formatMessage({id: 'type'})}>
                <Select style={{color: '#323232'}} value={type} onChange={value => {
                  setType(value)
                  setTransferAmount(0)
                }} size="large">
                  <Option value={'deposit'}>{intl.formatMessage({id: 'deposit'})}</Option>
                  <Option value={'withdraw'}>{intl.formatMessage({id: 'withdraw'})}</Option>
                </Select>
              </Form.Item>
            </Box>
            <Box mt={28}>
              <Form.Item label={intl.formatMessage({id: 'amount'})}>
                <InputNumber style={{color: '#323232', width: '100%'}} min={0} value={transferAmount} onChange={value => setTransferAmount(value)} size="large" />
              </Form.Item>
            </Box>
            <Box>
              <Form.Item {...buttonItemLayout}>
                <Flex>
                  <Text color="#969696" fontSize={14}>{intl.formatMessage({id: 'modal.transfer.label.available'})} {available}</Text>
                  <Text ml={42} fontSize={14} color="#1890ff"
                    sx={{ cursor: 'pointer' }}
                    onClick={() => setTransferAmount(available)}
                  >
                    {intl.formatMessage({id: 'modal.transfer.all'})}
                  </Text>
                </Flex>
              </Form.Item>
            </Box>
            {
              renderIf(available - transferAmount < 0)(
                <Box mt={10}>
                  <Form.Item {...buttonItemLayout}>
                    <Text mt={15} color="#F0505A" lineHeight="14px">{intl.formatMessage({id: 'modal.transfer.hint.notAvailable'})}</Text>
                  </Form.Item>
                </Box>
              )
            }
            {/* {
              renderIf(type === 'deposit')(
                <>
                  <Box mt={20}>
                    <Form.Item label=''>
                      <Input size="large" disabled value={`${gasEth} ETH $${utils.asLegalMoney(gasEth, 'ETH', 'USD')}`} />
                    </Form.Item>
                  </Box>
                  {renderIf(gasEth >= walletEthAvail)(
                    <Box mt={15}>
                      <Form.Item {...buttonItemLayout}>
                        <Text color="#F0505A" lineHeight="14px">{` ${walletEthAvail}ETH`}</Text>
                      </Form.Item>
                    </Box>
                  )}
                  <Box mt={28}>
                    <Form.Item {...buttonItemLayout}>
                      <Slider min={recommendedGasEth[0]} max={recommendedGasEth[2]} marks={{
                          [recommendedGasEth[0]]: '',
                          // [recommendedGasEth[1]]: '',
                          [recommendedGasEth[2]]: '',
                        }}
                        step={0.000001} value={gasEth} onChange={value => setGasEth(value)}
                      />
                    </Form.Item>
                  </Box>
                </>
              )
            } */}
            {/* {
              renderIf(type === 'withdraw')(
                <Box mt={28}>
                  <Form.Item label="">
                    <Input style={{color: '#323232', width: '100%'}} disabled value={'0.002ETH'} size="large" />
                  </Form.Item>
                  {
                    renderIf(walletEthAvail < withdrawFee)(
                      <Form.Item {...buttonItemLayout}>
                        <Text mt={15} color="#F0505A" lineHeight="14px">{` ${withdrawFee}ETH  ${walletEthAvail}ETH `}</Text>
                      </Form.Item>
                    )
                  }
                  {
                    renderIf(walletEthAvail >= withdrawFee)(
                      <Form.Item {...buttonItemLayout}>
                        <Text mt={15} color="#F0505A" lineHeight="14px">GASETH</Text>
                      </Form.Item>
                    )
                  }
                </Box>
              )
            } */}
            <Box mt={20}>
              <Form.Item {...buttonItemLayout}>
                <Button size="large" style={{width: '100%'}} type="primary" htmlType="submit">{intl.formatMessage({id: 'confirm'})}</Button>
              </Form.Item>
            </Box>
            {
              renderIf(type === 'deposit')(
                <Box>
                  <Text mt={30} color={"#969696"} fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.deposit.minerFee.hint1'})}</Text>
                  <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.deposit.minerFee.hint2'})}</Text>
                  <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.deposit.minerFee.hint3'})}</Text>
                </Box>
              )
            }
            {
              renderIf(type === 'withdraw')(
                <Box>
                  <Text mt={30} color={"#969696"} fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint1'})}</Text>
                  <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint2'})}</Text>
                  <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint3'})}</Text>
                  <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint4'})}</Text>
                  <Text mt={15} color="#969696" fontSize={12} lineHeight="20px">{intl.formatMessage({id: 'modal.transfer.withdraw.minerFee.hint5'})}</Text>
                </Box>
              )
            }
          </Form>
        </Box>
      </Box>
    </Modal>
  )
}

const mapStateToProps = ({ balance, walletBalance, rate, persistData }) => {
  return {
    balance,
    walletBalance,
    rate,
    persistData
  };
};

const mapDispatchToProps = ({ balance, transfer, walletBalance }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
    transfer: (type, symbol, amount, gasEth) => {
      let gasLimitInWEI = type === 'deposit' ? gasLimitDeposit : gasLimitWithdraw
      let gasPriceInWEI = utils.toWEI('ETH', gasEth) / gasLimitInWEI
      let gasPrice = common.formatter.toGWEI(gasPriceInWEI)
      return utils.withLoading(() => transfer[type]({symbol, amount, gasPrice}))
    }
  };
};

let connectedWidget = connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(Widget)

export default (props) => funcfy(connectedWidget, props)
