
import React from 'react'
import { Spin, Icon } from 'antd';

export default ({
  fontSize = 30,
  color = 'white',
  ...props
}) => {
  const antIcon = <Icon type="loading" style={{ fontSize, color }} spin />;
  return (
    <Spin indicator={antIcon} {...props} wrapperClassName="global-spin" />
  )
}