import numeral from 'numeral';
import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Spin } from 'components'
import { Table, Select, DatePicker } from 'antd';
import { ENUM_TX_STATUS } from 'utils/enum'
import utils from 'utils'
const { Option } = Select;
const { RangePicker } = DatePicker;

@injectIntl
class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filters: {
        symbol: undefined,
        side: undefined,
        status: undefined
      }
    }
  }

  async componentDidMount() {
    let { initialSymbol, initialSide } = this.props.history.location.data || {}
    if (initialSymbol) {
      await this.onSymbolChanged(initialSymbol)
    }
    if (initialSide) {
      await this.onSideChanged(initialSide)
    }
    this.props.fetchTransactions()
  }

  async onSymbolChanged (symbol) {
    this.setState(({ filters }) => {
      filters.symbol = symbol
      return { filters }
    })
    await this.props.updateFilterCond({ token: symbol })
  }

  async onSideChanged (side) {
    this.setState(({ filters }) => {
      filters.side = side
      return { filters }
    })

    let types = side && (side === 'WITHDRAW' ? ['onchainWithdrawal'] : ['deposit'])
    await this.props.updateFilterCond({ types })
  }

  async onStatusChanged (status) {
    this.setState(({ filters }) => {
      filters.status = status
      return { filters }
    })
    await this.props.updateFilterCond({ statuses: [status] })
  }

  renderFilters () {
    let { tokens: { list }, updateFilterCond, fetchTransactions, intl } = this.props
    const symbols = list.map(token => token.symbol)

    return (
      <Flex justifyContent="flex-end" flex={1}>
        <Select
          placeholder={intl.formatMessage({id: 'filters.allToken'})}
          allowClear
          style={{ width: 140, color: '#323232' }}
          value={this.state.filters.symbol}
          onChange={symbol => {
            this.onSymbolChanged(symbol)
            fetchTransactions()
          }}
        >
          {
            symbols.map(symbol => (
              <Option key={symbol} value={symbol}>{symbol}</Option>
            ))
          }
        </Select>
        <Select
          placeholder={intl.formatMessage({id: 'filters.allType'})}
          allowClear
          style={{ width: 180, color: '#323232', marginLeft: '20px', marginRight: '20px' }}
          value={this.state.filters.side}
          onChange={async (side) => {
            this.onSideChanged(side)
            fetchTransactions()
          }}
        >
          <Option value={'DEPOSIT'}><FormattedMessage id="deposit" /></Option>
          <Option value={'WITHDRAW'}><FormattedMessage id="withdraw" /></Option>
        </Select>
        <Select
          placeholder={intl.formatMessage({id: 'filters.allStatus'})}
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (status) => {
            this.onStatusChanged(status)
            fetchTransactions()
          }}
        >
          {
            _.map(ENUM_TX_STATUS.getOptions(), status => (
              <Option key={status.value} value={status.value}>
                <FormattedMessage id={`enum.txStatus.${status.value}`} />
              </Option>
            ))
          }
        </Select>
        <RangePicker placeholder={[
            intl.formatMessage({id: 'startDate'}),
            intl.formatMessage({id: 'endDate'})
          ]}
          onChange={async range => {
            let [start, end] = range
            start = start && start.startOf('day').toDate().valueOf()
            end = end && end.endOf('day').toDate().valueOf()

            console.info({start, end})
            await updateFilterCond({
              start,
              end
            })
            fetchTransactions()
          }}
        />
      </Flex>
    )
  }

  render() {
    let { transfer, intl } = this.props
    // TODO:  render 
    
    let transfers = transfer.list.map(transfer => {
      let { symbol, amount, realAmount, feeAmount } = transfer
      return {
        ...transfer,
        amount: utils.fromWEI(symbol, amount),
        realAmount: utils.fromWEI(symbol, realAmount),
        feeAmount: utils.fromWEI('ETH', feeAmount)
      }
    })

    const transferColumns = [
      {
        title: intl.formatMessage({id: 'tokenName'}),
        dataIndex: 'symbol',
        key: 'symbol',
        width: 90,
        render: (symbol, record) => {
          return (
            <Flex>
              <Text>{symbol}</Text>
            </Flex>
          )
        },
      },
      {
        title: intl.formatMessage({id: 'timestamp'}),
        dataIndex: 'timestamp',
        key: 'timestamp',
        width: 250,
        render: (timestamp, row) => {
          return (<Text>{moment(timestamp).format('YYYY-MM-DD HH:mm:ss')}</Text>)
        }
      },
      {
        title: intl.formatMessage({id: 'type'}),
        dataIndex: 'txType',
        key: 'txType',
        width: 150,
        render: (txType, row) => {
          return (<Text>{intl.formatMessage({id: txType === 'deposit' ? 'deposit' : 'withdraw'})}</Text>)
        }
      },
      {
        title: intl.formatMessage({id: 'amount'}),
        dataIndex: 'realAmount',
        key: 'realAmount',
        width: 150,
        render: (realAmount, row) => (
          <Text>{numeral(realAmount).format('0,0.[00000000]')}</Text>
        )
      },
      {
        title: intl.formatMessage({id: 'fee'}),
        dataIndex: 'feeAmount',
        key: 'feeAmount',
        width: 150,
        render: (feeAmount, row) => (
          <Text>{numeral(feeAmount).format('0,0.[00000000]')} ETH</Text>
        )
      },
      {
        title: intl.formatMessage({id: 'status'}),
        dataIndex: 'status',
        key: 'status',
        width: 50,
        render: (status, row) => (
          <Text><FormattedMessage id={`enum.txStatus.${status}`} /></Text>
        )
      },
      {
        title: intl.formatMessage({id: 'mine.transfer.list.header.hash'}),
        align: 'right',
        dataIndex: '',
        key: 'ops',
        width: 200,
        render: (status, row) => (
          <Flex justifyContent='flex-end'>
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={async () => {
                window.open(`https://cn.etherscan.com/tx/${row.txHash}`, '_blank')
              }}
            >
              {`${_.take(row.txHash, 7).join('')}...${_.takeRight(row.txHash, 7).join('')}`}
            </Text>
          </Flex>
        )
      },
      
    ];

    return (
      <Box>
        { this.renderFilters() }
        <Spin spinning={transfer.loading} color="#0064FF">
          <Table rowKey="txHash" columns={transferColumns} dataSource={transfers}
            className="mine-table"
            pagination={false}
            style={{ minHeight: '450px' }}
            // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
          />
        </Spin>
        {/* <Pagination ref={pagination => this.pagination = pagination} fetchData={this.fetchData} onFetched={this.onDataFetched}></Pagination> */}
      </Box>
    )
  }
}

const mapStateToProps = ({ transfer, markets, tokens }) => {
  return {
    transfer,
    markets,
    tokens
  };
};

const mapDispatchToProps = ({ transfer }) => {
  return {
    fetchTransactions: transfer.fetchTransactions,
    updateFilterCond: transfer.updateFilterCond,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))