export function formatResolution (str) {
  if (str === 'D') {
    return 24 * 60
  } else if (str === 'W') {
    return 7 * 24 * 60
  } else {
    return Number(str)
  }
}

function fromWEI (symbol, valueInWEI, precision, ceil = false) {
  let token = store.select.tokens.getTokenBySymbol(store.getState(), symbol)

  if (!token) {
    return 0
  }
  const value = BigNumber(valueInWEI).div("1e" + token.digits)
  precision = precision || token.precision

  if (ceil) {
    return value.decimalPlaces(token.precision).toNumber()
  } else {
    return value.toNumber().toFixed(precision)
  }
}

function toWEI (symbol, value) {
  let token = store.select.tokens.getTokenBySymbol(store.getState(), symbol)

  if (!token) {
    return 0;
  }
  const valueInBN = BigNumber(value).times("1e" + token.digits)
  return valueInBN.toString(10)
}

