import React from 'react';
import { connect } from 'react-redux';
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import {
  Trade,
  TVChart,
  PlaceOrder,
  MyOrder,
  Depth,
  EggAward
} from 'widgets'
import { BillPreCheck } from 'widgets/Common'
import utils from 'utils'

const TOP_NAV_HEIGHT = 50
const PLACE_ORDER_HEIGHT = 243
const DIVIDER_SIZE = 3
const TADEINGVIEW_MIN_HEIGHT = 285

class Home extends React.Component {
  constructor(props) {
    super(props);
    // this.billPreCheck = React.createRef();
    this.state = {
      windowHeight: 747,
      largeScreen: false
    }
  }

  onWindowResize = function () {
    const windowHeight = document.body.clientHeight
    const windowWidth = document.body.clientWidth

    this.setState({
      windowHeight,
      largeScreen: windowWidth >= 1920
    })
  }.bind(this)

  componentDidMount() {
    window.addEventListener('resize', this.onWindowResize);
    this.onWindowResize()

    this.billPreCheck.checkBrowser()
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  render() {
    let { windowHeight } = this.state
    let bodyHeight = windowHeight - TOP_NAV_HEIGHT - 2 * DIVIDER_SIZE
    let leftRemainHeight = bodyHeight - DIVIDER_SIZE
    let rightRemainHeight = bodyHeight - DIVIDER_SIZE

    return (
      <Flex variant='exchange' width="100%">
        <Flex flex={1} flexDirection="column">
          <TVChart height={_.max([leftRemainHeight * 500 / (500 + 200), TADEINGVIEW_MIN_HEIGHT])} />
          <Box as='hr' variant='divider.exchangeX' />
          <MyOrder height={_.min([leftRemainHeight * 200 / (500 + 200), leftRemainHeight - TADEINGVIEW_MIN_HEIGHT])} />
        </Flex>
        <Box as='hr' variant='divider.exchangeY' />
        <Flex width={425 + 300 + DIVIDER_SIZE} flexDirection='column'>
          <Flex >
            <Depth width={425} height={rightRemainHeight - PLACE_ORDER_HEIGHT} />
            <Box as='hr' height={rightRemainHeight - PLACE_ORDER_HEIGHT} variant='divider.exchangeY' />
            <Trade width={300} height={rightRemainHeight - PLACE_ORDER_HEIGHT} px={14} />
          </Flex>
          <Box as='hr' variant='divider.exchangeX' />
          <PlaceOrder />
        </Flex>
        <BillPreCheck ref={ref => this.billPreCheck = ref}></BillPreCheck>
        <EggAward />
      </Flex>
    )
  }
}

const mapStateToProps = state => {
  return {
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home);