import _ from 'lodash'
import numeral from 'numeral'
import utils from 'utils'
import api from 'api'

const maxTradeCount = 40
const initState = {
  loading: false,
  list: [],
  latestPrice: 0,
  lastPrice: 0,
  variation: 0,
  page: 1,
  pageSize: 10
}

function formatFromWEI (trades, currentMarket) {
  let { baseToken: baseSymbol, quoteToken: quoteSymbol, pricePrecision, amountPrecision, totalPrecision } = currentMarket

  trades = trades.map(trade => {
    trade.side = _.toUpper(trade.side)
    trade.timestamp = Number(trade.timestamp)
    trade.price = Number(trade.price).toFixed(pricePrecision)
    trade.size = utils.fromWEI(baseSymbol, Number(trade.size), amountPrecision)
    return trade
  })

  return trades
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    updateVariation: (state, latestPrice) => {
      let lastPrice = state.latestPrice
      let variation = 0
      if (lastPrice && latestPrice) {
        variation = (latestPrice - lastPrice) / lastPrice
      }
      return {
        ...state,
        lastPrice,
        latestPrice,
        variation
      }
    }
  },
  effects: dispatch => ({
    async fetchTrade (payload, { markets, trade: tradeStore }) {
      if (!markets.currentMarket) return
      let { market: marketPair } = markets.currentMarket

      //  trade 
      dispatch.socket.unsubscribe([{
        topic: 'trade',
        unsubscribeAll: true
      }])
      dispatch.socket.subscribe([{
        topic: 'trade',
        market: marketPair,
      }])

      this.update({
        list: [],
        loading: true,
      })

      var trades = await api.getTrade(marketPair, maxTradeCount);
      if (!trades || trades.length === 0) {
        this.update({ loading: false })
        return
      }
      trades = formatFromWEI(trades, markets.currentMarket)
      trades = trades.map((item, i) => {
        item.index = i
        return item
      })

      this.update({
        list: trades,
        loading: false
      })

      let currentPrice = trades[0].price
      this.updateVariation(currentPrice)
      document.title = `${currentPrice}|${marketPair}|WeDEX`
    },
    async incrementalUpdate (trades, { trade: tradeState, markets }) {
      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      // array to object
      trades = trades.map(trade => {
        let [timestamp, tradeId, side, size, price, fee] = trade
        return { timestamp, tradeId, side, size, price, fee }
      })
      trades = formatFromWEI(trades, markets.currentMarket)
      
      // 
      trades = _.orderBy(trades, ['timestamp'], ['desc'])
      let { list } = tradeState
      list.unshift(...trades)
      //  dom
      let modified = _.take(list, maxTradeCount)
      modified = modified.map((item, i) => {
        item.index = i
        return item
      })

      this.update({
        list: modified,
      })

      let currentPrice = modified[0].price
      this.updateVariation(currentPrice)
      document.title = `${currentPrice}|${marketPair}|WeDEX`
    },
  })
}