import { ethereum } from 'librarys/lightcone_v3';
import appConfig from 'config/appConfig'
var detect = require('detect-browser').detect;
let { chainId } = appConfig

// DKEY:
// isDkey()  boolean  
// enable(success: String, error: String)  
// getBalance(success: String, error: String)  
// sign(params: String, success: String, error: String)  
// networkVersion()  string

const initState = {
  startConnecting: false,
  isDesiredBrowser: false,
  isDesiredNetwork: false,
  installed: false,
  walletName: 'metamask',
  enable: false,
}

async function getAddress (walletName) {
  if (walletName === 'metamask') {
    console.log(' metamask');
    const accounts = await window.ethereum.enable();
    let address = accounts[0]
    if (!address) {
      console.log(`metamask `);
      return Promise.reject('')
    }
    console.log(`metamask : ${address}`);

    window.wallet = ethereum.account.fromMetaMask(window.web3, address, address)
    return address
  } else if (walletName === 'dkey') {
    console.log(' dkey');
    let promise = new Promise((resolve, reject) => {
      window.dkeyEnableSuccess = function (resp) {
        resolve(resp.result)
      }
      window.dkeyEnableFailure = function (error) {
        reject(error)
      }
      window.ethereum.enable('dkeyEnableSuccess', 'dkeyEnableFailure')
    })
    const address = await promise
    if (!address) {
      console.log(`dkey `);
      return Promise.reject('')
    }
    console.log(`dkey : ${address}`);

    window.wallet = ethereum.account.fromDkey(window.web3, address, address)
    return address
  }
}

export default {
  state: initState,
  reducers: {
    updateMetaMask: (state, payload) => ({ ...state, ...payload }),
  },
  effects: dispatch => ({
    async connectToMetaMask (payload, { metaMask, dexAccount }) {
      this.updateMetaMask({ startConnecting: true })

      const browser = detect()
      let valid = _.includes(['firefox', 'chrome', '360'], browser.name)
      this.updateMetaMask({ isDesiredBrowser: valid })

      if (typeof window.ethereum === 'undefined') {
        this.updateMetaMask({
          installed: false,
          startConnecting: false
        })
        return
      }

      if (window.ethereum.isMetaMask) {
        this.updateMetaMask({
          installed: true,
          walletName: 'metamask'
        })

        window.ethereum.autoRefreshOnNetworkChange = false;
        // window.ethereum.networkVersion 1s
        setTimeout(() => {
          const desireNetwork = chainId
          this.updateMetaMask({
            isDesiredNetwork:
              desireNetwork === Number(window.ethereum.networkVersion)
          })
        }, 1000)

        window.ethereum.on('networkChanged', networkVersion => {
          console.info('### networkChanged')
          const desireNetwork = chainId
          this.updateMetaMask({
            isDesiredNetwork: desireNetwork === Number(networkVersion)
          });
        });
        window.ethereum.on('accountsChanged', accounts => {
          console.info('### accountsChanged')
          let address = accounts[0]
          if (!address) return
          window.wallet = ethereum.account.fromMetaMask(window.web3, address, address)
          dispatch.dexAccount.getAccount(address)
        })
      } else if (window.ethereum.isDkey) {
        const desireNetwork = chainId
        this.updateMetaMask({
          isDesiredNetwork: desireNetwork === Number(window.ethereum.networkVersion()),
          installed: true,
          walletName: 'dkey'
        })
      } else {
        this.updateMetaMask({
          installed: false,
          startConnecting: false
        })
        return
      }

      this.updateMetaMask({ startConnecting: false })
      await dispatch.metaMask.initAccountFromMetaMask()
    },
    // 
    //  DKEY
    // DKEY 
    async initAccountFromMetaMask (payload, { metaMask }) {
      // metaMask.enable 
      if (metaMask.enable) return

      let address = await getAddress(metaMask.walletName)

      if (!address) {
        dispatch.appState.update({
          notification: {
            type: 'info',
            message: 'notify.metamask.needLogin'
          },
        })
        return
      }

      // window.wallet = ethereum.account.fromMetaMask(window.web3, address, address)
      // dexAccount
      await dispatch.dexAccount.getAccount(address);

      this.updateMetaMask({ enable: true })
    }
  })
}