import numeral from 'numeral';
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Table, Dropdown, Menu, Icon, Popover } from 'antd'
import utils from 'utils'
import { Svg, Spin } from 'components'
import depthAllIcon from 'icons/depthAll.svg'
import depthSellIcon from 'icons/depthSell.svg'
import depthBuyIcon from 'icons/depthBuy.svg'
const BigNumber = require('bignumber.js');

const depthTypes = [
  { key: 'all', icon: depthAllIcon },
  { key: 'sell', icon: depthSellIcon },
  { key: 'buy', icon: depthBuyIcon },
]
@injectIntl
class Depth extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeType: depthTypes[0],
      hoverPrice: null,
    }
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  renderPrecisions () {
    let precisions = this.props.precisions()
    return (
      <Menu onClick={({key}) => {
        let { markets, setLevel } = this.props
        let precision = Number(key)
        let level = markets.currentMarket.pricePrecision - precision
        setLevel(level)
      }}>
      {
        precisions.map((precision) => (
          <Menu.Item key={precision}>
            <Text fontSize={[0, 0, 0]} color='hint'>{ precision }<FormattedMessage id="depth.operations.level"/></Text>
          </Menu.Item>
        ))
      }
      </Menu>
    )
  }

  render() {
    let { height, markets, depth, trade: { latestPrice, variation }, currentPrecision, order, intl } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { activeType } = this.state
    let { key: depthType } = activeType
    
    let tableHeight = (height - (40 + 30 + 30)) / 2
    let scrollHeight = depthType === 'all' ? tableHeight : tableHeight * 2

    let {asks, bids} = depth
    let max = _.maxBy([...asks, ...bids], item => Number(item.size))
    let maxSize = max ? max.size : 100

    const columns = [
      {
        align: 'left',
        title: (col) => `${intl.formatMessage({id: 'price'})}(${currentMarket.quoteToken})`,
        dataIndex: 'price',
        width: 85,
        render: (price, row) => (
          <Text color={row.type === 'ask' ? "failure" : "success"}>{price}</Text>
        )
      },
      {
        align: 'right',
        title: (col) => `${intl.formatMessage({id: 'size'})}(${currentMarket.baseToken})`,
        dataIndex: 'size',
        width: 100,
        render: size => (
          <Text>{size}</Text>
        )
      },
      {
        align: 'right',
        title: (col) => `${intl.formatMessage({id: 'depth.list.header.volume'})}(${currentMarket.quoteToken})`,
        dataIndex: 'volume',
        width: 100,
        render: volume => (
          <Text>{volume}</Text>
        )
      },
      {
        align: 'right',
        title: (col) => ``,
        key: 'myOrder',
        width: 105,
        render: ({price}) => {
          let { processingList } = order
          let sumSize = _.chain(processingList)
            .filter({price})
            .reduce((sum, o) => {
              let unfilled = Number(o.size) - Number(o.filledSize)
              sum += unfilled
              return sum
            }, 0)
            .value()
          return (
            <Text>{sumSize.toFixed(currentMarket.amountPrecision)}</Text>
          )
        }
      },
      {
        // dataIndex: 'size',
        width: 0,
        key: 'popover',
        className: 'col-percentage',
        render: (size, row) => {
          const content = (
            <Box width={200} >
              <Flex justifyContent="space-between">
                <Box>:</Box>
                <Box>{(row.sumVolume / row.sumSize).toFixed(currentMarket.pricePrecision)}</Box>
              </Flex>
              <Flex justifyContent="space-between">
                <Box>{currentMarket.baseToken}:</Box>
                <Box>{row.sumSize}</Box>
              </Flex>
              <Flex justifyContent="space-between">
                <Box>{currentMarket.quoteToken}:</Box>
                <Box>{row.sumVolume}</Box>
              </Flex>
            </Box>
          )
          return (
            <Popover content={content} trigger={"click"} placement="right" mouseEnterDelay={0} mouseLeaveDelay={0}
              visible={row.price === this.state.hoverPrice}
              overlayStyle={{}}
            >
              <Box sx={{
                position: "absolute",
                left: '0',
                right: '0',
                height: '18px',
                opacity: 0
              }}>
              </Box>
            </Popover>
          )
        }
      },
      {
        // dataIndex: 'size',
        width: 0,
        key: 'background',
        className: 'col-percentage',
        render: (size, row) => (
          <Box sx={{
            position: "absolute",
            right: '0',
            width: numeral(row.size / maxSize).format('0%'),
            height: '18px',
          }}>
            <Box bg={row.type === 'ask' ? "#F0505A" : "#50BE8C"} sx={{
              // position: "relative",
              // bottom: '9px',
              height: '100%',
              opacity: 0.15,
            }}></Box>
          </Box>
        )
      },
    ];

    const onRowEvent = record => {
      return {
        onClick: event => {
          let { type, price, sumSize } = record
          let total = (new BigNumber(price).times(new BigNumber(sumSize))).toFixed(currentMarket.totalPrecision)
          this.props.updatePlaceOrder({
            buyPrice: record.price,
            // buyAmount: type === 'ask' ? sumSize : null,
            // buyTotal: type === 'ask' ? total : null,
            buyAmount: sumSize,
            buyTotal: total,
            marketBuyTotal: null,
          })
          this.props.updatePlaceOrder({
            sellPrice: record.price,
            // sellAmount: type === 'bid' ? sumSize : null,
            // sellTotal: type === 'bid' ? total : null,
            sellAmount: sumSize,
            sellTotal: total,
            marketSellAmount: null,
          })
        },
        onMouseEnter: event => {
          this.setState({hoverPrice: record.price})
        },
        onMouseLeave: event => {
          this.setState({hoverPrice: null})
        },
      }
    }

    return (
      <Box width={this.props.width}>
        <Flex px={14} height={40} alignItems="center" justifyContent="flex-start">
          <Text fontSize={0} fontWeight="bolder"><FormattedMessage id="depth.title"/></Text>
          <Flex ml={'5px'}>
            {
              depthTypes.map(type => (
                <Flex key={type.key} width={16} height={14} mx={'4px'} variant="flexCenter" sx={{
                    borderRadius: '2px',
                    border: `1px solid ${this.state.activeType === type ? '#0064FF' : '#32374b'}`,
                    cursor: 'pointer',
                  }}
                  onClick={() => {
                    this.setState({ activeType: type })
                    this.props.scrollSellTable()
                  }}
                >
                  <Image width={10} height={6} src={type.icon} />
                </Flex>
              ))
            }
          </Flex>
          <Box flex={1}></Box>
          <Dropdown overlay={this.renderPrecisions()} placement="bottomCenter" overlayClassName="nav-menu">
            <Flex height={40} lineHeight={'40px'} alignItems="center">
              {currentPrecision()}<FormattedMessage id="depth.operations.level"/>
              <Icon type="caret-down" />
            </Flex>
          </Dropdown>
        </Flex>
        <Flex height={30} alignItems="center" px={14}>
          <Box px={`2px`} fontSize="12" lineHeight={`20px`} color="#646478" flex={1}>{`${intl.formatMessage({id: 'price'})}(${currentMarket.quoteToken})`}</Box>
          <Box px={`2px`} fontSize="12" lineHeight={`20px`} color="#646478" width={105} textAlign="right">{`${intl.formatMessage({id: 'size'})}(${currentMarket.baseToken})`}</Box>
          <Box px={`2px`} fontSize="12" lineHeight={`20px`} color="#646478" width={105} textAlign="right">{`${intl.formatMessage({id: 'depth.list.header.volume'})}(${currentMarket.quoteToken})`}</Box>
          <Box px={`2px`} fontSize="12" lineHeight={`20px`} color="#646478" width={105} textAlign="right">{``}</Box>
        </Flex>
        {
          utils.renderIf(depthType === 'all' || depthType === 'sell')(
            <Box px={14}>
              <Spin spinning={depth.loading}>
                <Table rowKey="price" px={14} columns={columns} dataSource={depth.asks} onRow={onRowEvent} align="right"
                  className="exchange-table sell-table"
                  pagination={false}
                  scroll={{ y: scrollHeight }}
                  style={{ height: scrollHeight }}
                  showHeader={false}
                  // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
                />
              </Spin>
            </Box>
          )
        }
        <Flex px={14} height={30} fontWeight="bold" bg="backgroundTint" alignItems="center">
          <Text mr={'8px'} fontSize={'16px'} color={variation >= 0 ? 'success' : 'failure'}>{latestPrice}</Text>
          <Svg color={variation >= 0 ? '#50BE8c' : '#F0505A'} icon={variation >= 0 ? 'icon-arrow-up' : 'icon-arrow-down'} size="25px"></Svg>
          <Text ml={'8px'} fontSize={'16px'} color="#3c3c50">$ {utils.asLegalMoney(latestPrice, currentMarket.quoteToken, 'USD')}</Text>
        </Flex>
        {
          utils.renderIf(depthType === 'all' || depthType === 'buy')(
            <Box px={14}>
              <Spin spinning={depth.loading}>
                <Table rowKey="price" px={14} columns={columns} dataSource={depth.bids} onRow={onRowEvent} align="right" 
                  className="exchange-table"
                  pagination={false}
                  scroll={{ y: scrollHeight }}
                  showHeader={false}
                  style={{ height: scrollHeight }}
                  // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
                />
              </Spin>
            </Box>
          )
        }
      </Box>
    )
  }
}

const mapStateToProps = ({ markets, depth, trade, rate, order }) => {
  return {
    markets,
    depth,
    trade,
    rate,
    order,
    precisions: () => {
      let market = markets.currentMarket
      if (!market) return []

      let { pricePrecision, maxLevel } = market
      return _.range(pricePrecision, pricePrecision - maxLevel)
    },
    currentPrecision: () => {
      if (!markets.currentMarket) return 0
      return markets.currentMarket.pricePrecision - depth.level
    }
  };
};

const mapDispatchToProps = ({ depth, placeOrder }) => {
  return {
    setLevel: depth.setLevel,
    scrollSellTable: depth.scrollSellTable,
    updatePlaceOrder: placeOrder.update
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Depth);