import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import Balance from './Balance';
import Transfer from './Transfer';
import Order from './Order';
import Apikey from './Apikey';
import Award from './Award';
import { UpsertPasswordModal } from 'widgets/Dialog'

const tabs = [
  { key: 'balance', render: () => <Balance/> },
  { key: 'transfer', render: () => <Transfer/> },
  { key: 'order', render: () => <Order/> },
  { key: 'apikey', render: () => <Apikey/> },
  { key: 'award', render: () => <Award/> },
  { key: 'password' },
]
@injectIntl
class Mine extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: tabs[0].key
    }
  }

  componentDidMount() {
    console.info(this.props.location)
    let { tab } = this.props.match.params
    this.setState({activeTab: tab})
  }

  componentWillReceiveProps(newProps) {
    let { tab: oldTab } = this.props.match.params
    let { tab: newTab } = newProps.match.params
    if (oldTab !== newTab) {
      this.setState({activeTab: newTab})
    }
  }

  renderContent () {
    let {activeTab} = this.state
    let tab = _.find(tabs, { key: activeTab })
    return tab.render && tab.render()
  }

  render() {
    let { persistData: { locale } } = this.props

    return (
      <Flex pt={90} variant={'mine'} fontSize={14}>
        <Flex flexDirection="column" sx={{
          position: "relative",
          borderLeft: '1px solid #F0F0F0',
          borderTop: '1px solid #F0F0F0',
        }}>
          <Flex sx={{
            position: "absolute",
            left: 0,
            top: "-65px",
            alignItems: 'center',
            color: '#0078FF',
            cursor: 'pointer'
          }} onClick={() => this.props.history.push(`/`)}>
            <Text mr="5px" fontSize="18px">{"<"}</Text><Text sx={{ borderBottom: "1px solid #0078FF" }}></Text>
          </Flex>
        {
          tabs.map(tab => (
            <Box key={tab.key} variant="flexCenter" width={150} height={50} lineHeight={'50px'} sx={{
                bg: tab.key === this.state.activeTab ? '#eff7ff' : 'white',
                color: tab.key === this.state.activeTab ? '#0078FF' : 'black',
                cursor: 'pointer',
                borderRight: '1px solid #F0F0F0',
                borderBottom: '1px solid #F0F0F0',
                textAlign: 'center',
              }} onClick={async () => {
                if (tab.key === 'password') {
                  await UpsertPasswordModal()
                  return
                }
                this.props.history.push(`/${locale}/mine/${tab.key}`)
              }}
            >
              <FormattedMessage id={`mine.nav.${tab.key}`} />
            </Box>
          ))
        }
        </Flex>
        <Box width={1100} ml={60} mb={20}>
        {
          this.renderContent()
        }
        </Box>
      </Flex>
    )
  }
}

const mapStateToProps = ({ persistData }) => {
  return {
    persistData
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Mine))