import _ from 'lodash'
import React from 'react';
import numeral from 'numeral'
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Tooltip, Icon, Popover } from 'antd';
import { TabNav, CoinInput, BillPreCheck } from './Common'
import { TransferModal } from 'widgets/Dialog'
import utils, { notification } from 'utils'

const BigNumber = require('bignumber.js');
const percentPositins = [ 0.25, 0.5, 0.75, 1 ]
@injectIntl
class PlaceOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 'limitPrice'
    }
  }

  componentDidMount() {
  }

  renderLimitBuyArea (min, available) {
    let { markets, intl } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken, pricePrecision, amountPrecision, totalPrecision } = currentMarket
    let { placeOrder: { buyPrice, buyAmount, buyTotal }, updatePlaceOrder, placeBuyOrder } = this.props
    let signined = utils.checkLogined()

    let error = null
    if (buyTotal) {
      min = Number(min)
      available = Number(available)
      // buyTotal
      // buyTotal = Number(buyTotal)
      let total = Number(buyTotal)
      if (total < min) {
        error = intl.formatMessage({id: 'placeOrder.hint.buyLessThanMin'}, { min, quoteToken })
      } else if (total > available) {
        error = intl.formatMessage({id: 'placeOrder.hint.moreThanAvalable'}, { available, token: quoteToken })
      }
    }

    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10}>
        <Flex mt={'7px'} alignItems="center">
          <CoinInput
            label={intl.formatMessage({id: "placeOrder.input.label.buyPrice"})}
            precision={pricePrecision}
            value={buyPrice}
            approximate={`$ ${utils.asLegalMoney(buyPrice, quoteToken, 'USD')}`}
            onChange={e => {
              let price = e.target.value
              let buyTotal = (new BigNumber(buyAmount || 0)).times(new BigNumber(price || 0))
              updatePlaceOrder({
                //  price 0 
                buyPrice: price,
                buyTotal: buyTotal.toFixed(totalPrecision)
              })
            }}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'7px'} alignItems="center">
          <CoinInput
            label={intl.formatMessage({id: "placeOrder.input.label.buySize"})}
            precision={amountPrecision}
            value={buyAmount}
            onChange={e => {
              let amount = e.target.value
              let total = (new BigNumber(amount || 0)).times(new BigNumber(buyPrice || 0))
              updatePlaceOrder({
                //  value 0 
                buyAmount: amount,
                buyTotal: total.toFixed(totalPrecision),
              })
            }}
            unit={baseToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'7px'} alignItems="center">
          <CoinInput
            label={intl.formatMessage({id: "placeOrder.input.label.total"})}
            precision={totalPrecision}
            error={error}
            approximate={`$ ${utils.asLegalMoney(buyTotal, quoteToken, 'USD')}`}
            unit={quoteToken}
            value={buyTotal}
            onChange={e => {
              let total = e.target.value
              let amount = buyPrice ? (new BigNumber(total || 0) / new BigNumber(buyPrice)) : 0
              updatePlaceOrder({
                buyAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
                //  total 0 
                buyTotal: total
              })
            }}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={11} justifyContent="space-between" >
        {
          percentPositins.map(percent => (
            <Text key={percent} width={'23%'} height={22} lineHeight={'22px'}
              sx={{
                bg: 'rgba(255, 255, 255, 0.05)',
                color:"hint",
                cursor: 'pointer',
                textAlign: 'center',
                borderRadius: '2px'
              }}
              onClick={() => {
                let total = available * percent
                let amount = buyPrice ? (new BigNumber(total || 0) / new BigNumber(buyPrice)) : 0
                updatePlaceOrder({
                  buyAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
                  //  total 0 
                  buyTotal: total >= available ? _.floor(total, totalPrecision) : _.ceil(total, totalPrecision),
                })
              }}
            >
              {numeral(percent).format('0%')}
            </Text>
          ))
        }
        </Flex>
        <BillPreCheck disabled={buyTotal && !!error} onSuccess={() => {
          placeBuyOrder()
        }} depth={1}>
          {/* <Popover content={error} trigger={error ? "hover" : '' }> */}
            <Box mt={11} height={30} bg="success" sx={{
                borderRadius: '3px',
                cursor: 'pointer'
              }}
              textAlign="center" lineHeight="30px" fontSize={2} fontWeight="bold">
              {signined ? `${intl.formatMessage({id: 'buy'})}${baseToken}` : `${intl.formatMessage({id: 'placeOrder.hint.loginAndTrade'})}`}
            </Box>
          {/* </Popover> */}
        </BillPreCheck>
      </Flex>
    )
  }

  renderLimitSaleArea (min, available) {
    let { markets, intl } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken, pricePrecision, amountPrecision, totalPrecision } = currentMarket
    let { placeOrder: { sellPrice, sellAmount, sellTotal }, updatePlaceOrder, placeSellOrder } = this.props
    let signined = utils.checkLogined()

    let error = null
    if (sellAmount) {
      min = Number(min)
      available = Number(available)
      //  sellAmount 
      // sellAmount = Number(sellAmount)
      let amount = Number(sellAmount)
      if (amount < min) {
        error = intl.formatMessage({id: 'placeOrder.hint.saleLessThanMin'}, { min, baseToken })
      } else if (amount > available) {
        error = intl.formatMessage({id: 'placeOrder.hint.moreThanAvalable'}, { available, token: baseToken })
      }
    }

    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10}>
        <Flex mt={'7px'} alignItems="center">
          <CoinInput
            label={intl.formatMessage({id: "placeOrder.input.label.sellPrice"})}
            precision={pricePrecision}
            value={sellPrice}
            approximate={`$ ${utils.asLegalMoney(sellPrice, quoteToken, 'USD')}`}
            onChange={e => {
              let price = e.target.value
              let sellTotal = (new BigNumber(sellAmount || 0)).times(new BigNumber(price || 0))
              updatePlaceOrder({
                //  price 0 
                sellPrice: price,
                sellTotal: sellTotal.toFixed(totalPrecision)
              })
            }}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'7px'} alignItems="center">
          <CoinInput
            label={intl.formatMessage({id: "placeOrder.input.label.sellSize"})}
            precision={amountPrecision}
            error={error}
            value={sellAmount}
            onChange={e => {
              let amount = e.target.value
              let total = (new BigNumber(amount || 0)).times(new BigNumber(sellPrice || 0))
              updatePlaceOrder({
                //  amount 0 
                sellAmount: amount,
                sellTotal: total.toFixed(totalPrecision),
              })
            }}
            unit={baseToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'7px'} alignItems="center">
          <CoinInput
            label={intl.formatMessage({id: "placeOrder.input.label.total"})}
            precision={totalPrecision}
            approximate={`$ ${utils.asLegalMoney(sellTotal, quoteToken, 'USD')}`}
            unit={quoteToken}
            value={sellTotal}
            onChange={e => {
              let total = e.target.value
              let amount = sellPrice ? (new BigNumber(total || 0) / new BigNumber(sellPrice)) : 0
              updatePlaceOrder({
                sellAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
                //  total 0 
                sellTotal: total
              })
            }}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={11} justifyContent="space-between" >
        {
          percentPositins.map(percent => (
            <Text key={percent} width={'23%'} height={22} lineHeight={'22px'}
              sx={{
                bg: 'rgba(255, 255, 255, 0.05)',
                color:"hint",
                cursor: 'pointer',
                textAlign: 'center',
                borderRadius: '2px'
              }}
              onClick={() => {
                let amount = available * percent
                let total = (new BigNumber(amount || 0)).times(new BigNumber(sellPrice || 0))
                updatePlaceOrder({
                  //  amount 0 
                  sellAmount: _.floor(amount, amountPrecision),
                  sellTotal: total.toFixed(totalPrecision),
                })
              }}
            >
            {numeral(percent).format('0%')}
            </Text>
          ))
        }
        </Flex>
        <BillPreCheck disabled={sellAmount && !!error} onSuccess={() => placeSellOrder()} depth={1}>
          {/* <Popover content={error} trigger={error ? "hover" : '' }> */}
            <Box mt={11} height={30} bg="failure" sx={{
                borderRadius: '3px',
                cursor: 'pointer'
              }}
              textAlign="center" lineHeight="30px" fontSize={2} fontWeight="bold">
              {signined ? `${intl.formatMessage({id: 'sell'})}${baseToken}` : `${intl.formatMessage({id: 'placeOrder.hint.loginAndTrade'})}`}
            </Box>
          {/* </Popover> */}
        </BillPreCheck>
      </Flex>
    )
  }

  renderMarketBuyArea (available) {
    let { markets, depth, currentPrecision } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken } = currentMarket
    let { placeOrder: { marketBuyTotal }, updatePlaceOrder, placeBuyOrder } = this.props

    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10} pt={'3px'}>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}></Text>
          <CoinInput
            disabled={true}
            value={''}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}></Text>
          <CoinInput
            approximate={`$ 0.0`}
            value={marketBuyTotal}
            onChange={e => updatePlaceOrder({marketBuyTotal: e.target.value})}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={9} ml={40} >
        {
          percentPositins.map(percent => (
            <Flex key={percent} height={18} variant="flexCenter" lineHeight={'18px'} px={'6px'} py={'2px'}
              sx={{
                // background: '#141e32',
                // borderRadius: '2px',
                color:"hint",
                cursor: 'pointer'
              }}
              onClick={() => {
                let marketBuyTotal = (available * percent).toFixed(6)
                updatePlaceOrder({ marketBuyTotal })
              }}
            >
            {numeral(percent).format('0%')}
            </Flex>
          ))
        }
        </Flex>
        <BillPreCheck onSuccess={() => placeBuyOrder()} depth={1}>
          <Box mt={9} ml={40} height={34} bg="success" sx={{
              borderRadius: '3px',
              cursor: 'pointer'
            }}
            textAlign="center" lineHeight="34px" fontSize={2} fontWeight="bold">
            {baseToken}
          </Box>
        </BillPreCheck>
      </Flex>
    )
  }

  renderMarketSaleArea (available) {
    let { markets, depth, currentPrecision } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken } = currentMarket
    let { placeOrder: { marketSellAmount }, updatePlaceOrder, placeSellOrder } = this.props

    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10} pt={'3px'}>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}></Text>
          <CoinInput
            disabled={true}
            value={''}
            onChange={e => updatePlaceOrder({sellPrice: e.target.value})}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}></Text>
          <CoinInput
            value={marketSellAmount}
            onChange={e => updatePlaceOrder({marketSellAmount: e.target.value})}
            unit={baseToken}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={9} ml={40} >
        {
          percentPositins.map(percent => (
            <Text key={percent} height={18} lineHeight={'18px'}
              sx={{
                color:"hint",
                cursor: 'pointer'
              }}
              onClick={() => {
                let marketSellAmount = (available * percent).toFixed(6)
                updatePlaceOrder({ marketSellAmount })
              }}
            >
            {numeral(percent).format('0%')}
            </Text>
          ))
        }
        </Flex>
        <BillPreCheck onSuccess={() => placeSellOrder()} depth={1}>
          <Box mt={9} ml={40} height={34} bg="failure" sx={{
              borderRadius: '3px',
              cursor: 'pointer'
            }}
            textAlign="center" lineHeight="34px" fontSize={2} fontWeight="bold">
            {baseToken}
          </Box>
        </BillPreCheck>
      </Flex>
    )
  }

  getMinTradeValue (symbol) {
    let mapping = {
      'USDT': 2,
      'LRC': 100,
      'ETH': 0.01,
      'DKEY': 10,
    }
    return mapping[symbol] || 0
  }

  render() {
    let { markets: { currentMarket }, balance: { list: balances }, dexAccount: { passwordHash }, intl } = this.props
    if (!currentMarket) return <></>

    const tabs = [
      { text: intl.formatMessage({id: 'placeOrder.tabs.limitPrice'}), value: 'limitPrice' },
      // { text: intl.formatMessage({id: 'placeOrder.tabs.marketPrice'}), value: 'marketPrice' },
    ]

    let { baseToken, quoteToken, pricePrecision, amountPrecision } = currentMarket
    let baseBalance = _.find(balances, balance => balance.token.symbol === baseToken)
    let quoteBalance = _.find(balances, balance => balance.token.symbol === quoteToken)
    let availBase = baseBalance ? baseBalance.availAmount : 0
    let availQuote = quoteBalance ? quoteBalance.availAmount : 0
    return (
      <Box>
        <TabNav tabs={tabs} onChange={tab => this.setState({activeTab: tab.value})}>
          <Box flex={1}></Box>
          <Text color="hint"><FormattedMessage id="placeOrder.title.available" /> {baseToken}: {availBase.toFixed(amountPrecision)}</Text>
          <Text color="hint" ml={20}><FormattedMessage id="placeOrder.title.available" /> {quoteToken}: {availQuote.toFixed(pricePrecision)}</Text>
          <Text mx={15} color="blue" onClick={() => {
            if (!passwordHash) {
              notification.info({ message: intl.formatMessage({id: 'placeOrder.notify.needLogin'}) })
              return
            }
            TransferModal()
          }}
            sx={{ cursor: 'pointer' }}
          >
            <FormattedMessage id="placeOrder.operations.transfer" />
          </Text>
        </TabNav>
        {
          utils.renderIf(this.state.activeTab === 'limitPrice')(
            <Flex bg="backgroundTint" height={203}>
              {this.renderLimitBuyArea(this.getMinTradeValue(quoteToken), availQuote)}
              {this.renderLimitSaleArea(this.getMinTradeValue(baseToken), availBase)}
            </Flex>
          )
        }
        {
          utils.renderIf(this.state.activeTab === 'marketPrice')(
            <Flex bg="backgroundTint" height={203}>
              {this.renderMarketBuyArea(availQuote)}
              {this.renderMarketSaleArea(availBase)}
            </Flex>
          )
        }
      </Box>
    )
  }
}

const mapStateToProps = ({ placeOrder, markets, balance, dexAccount, rate }) => {
  return {
    placeOrder,
    markets,
    balance,
    dexAccount,
    rate
  };
};

const mapDispatchToProps = ({ placeOrder }) => {
  return {
    placeBuyOrder: placeOrder.placeBuyOrder,
    placeSellOrder: placeOrder.placeSellOrder,
    updatePlaceOrder: placeOrder.update
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PlaceOrder);