import moment from 'moment';
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Table, Icon } from 'antd';
import { Spin } from 'components';

@injectIntl
class Trade extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }
  
  componentWillUnmount() {
  }

  render() {
    let { trade, markets, intl, height } = this.props
    let tableHeight = height - 40 - 30


    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    const columns = [
      {
        align: 'left',
        title: `${intl.formatMessage({id: 'price'})}(${currentMarket.quoteToken})`,
        dataIndex: 'price',
        width: 82,
        render: (price, row) => (
          <Text color={row.side === 'BUY' ? "success" : "failure"}>{row.price}</Text>
        )
      },
      {
        align: 'right',
        title: `${intl.formatMessage({id: 'size'})}(${currentMarket.baseToken})`,
        dataIndex: 'size',
        width: 82,
      },
      {
        align: 'right',
        title: `${intl.formatMessage({id: 'timestamp'})}`,
        dataIndex: 'timestamp',
        render: (unused, record) => {
          return (<Text>{moment(record.timestamp).format('HH:mm:ss')}</Text>)
        }
      },
    ];

    return (
      <Box {...this.props} className="trade-panel">
        <Flex height={40} alignItems="center" justifyContent="space-between">
          <Text fontSize={0} fontWeight="bold"><FormattedMessage id="trade.title"/></Text>
          {/* <Text fontSize={0} color='hint'></Text> */}
        </Flex>
        <Spin spinning={trade.loading}>
          <Table columns={columns} dataSource={trade.list} align="right"
            rowKey="index"
            style={{ height: tableHeight }}
            scroll={{ y: tableHeight }}
            className="exchange-table"
            pagination={false}
            // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
          />
        </Spin>
      </Box>
    )
  }
}

const mapStateToProps = ({ trade, markets }) => {
  return {
    trade,
    markets
  };
};

const mapDispatchToProps = ({ trade }) => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Trade);