import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { ConnectedRouter } from 'connected-react-router';
import Routes from './routes';
import { connect } from 'react-redux';
import { IntlProvider } from 'react-intl'
import { messages } from './locales'
import { Spin } from 'antd';
import {
  Box
} from 'rebass'
import './App.less';

const App = ({ history, ready, loading, locale }) => {
  //  persist/REHYDRATE 
  if (!ready) return (
    <Box pt="30%" textAlign="center">
      <Spin size="large" />
    </Box>
  )

  return (
      <IntlProvider locale={locale} key={locale} messages={messages[locale]}>
        <Spin className="body-loading" spinning={loading} size="large" style={{height: '100vh', maxHeight: 'unset', zIndex:1001}}>
          <ConnectedRouter history={history}>
            <Routes />
          </ConnectedRouter>
        </Spin>
      </IntlProvider>
  )
};

App.propTypes = {
  history: PropTypes.object
};

const mapState = ({ appState, persistData }) => {
  return {
    ready: appState.ready,
    loading: appState.loading,
    locale: persistData.locale,
  }
}

const mapDispatch = ({ metaMask, markets, socket }) => {
  return {
    fetchMarkets: markets.fetchMarkets,
    startSocket: socket.start
  };
};

export default connect(mapState, mapDispatch)(App)