import _ from 'lodash'
import api from 'api'
import utils from 'utils'

const initDeepthState = {
  loading: false,
  level: 0,
  asks: [],
  bids: [],
  incrementalVersion: null
}

function scrollSellTableToBottom () {
  let tableDom = document.getElementsByClassName('sell-table')[0]
  if (!tableDom) return
  let tableBodyDom = tableDom.getElementsByClassName('ant-table-body')[0]
  if (!tableBodyDom) return
  tableBodyDom.scrollTop = tableBodyDom.scrollHeight
}

function formatFromWEI (resp, currentMarket, level) {
  let { asks, bids } = resp
  let { baseToken: baseSymbol, quoteToken: quoteSymbol, pricePrecision, amountPrecision, totalPrecision } = currentMarket

  asks = asks.map(ask => ({
    ...ask,
    type: 'ask',
    //  toFixed  0, 23.120000
    //  number 
    price: Number(ask.price).toFixed(pricePrecision - level),
    size: utils.fromWEI(baseSymbol, ask.size, amountPrecision),
    volume: utils.fromWEI(quoteSymbol, ask.volume, totalPrecision)
  }))
  bids = bids.map(bid => ({
    ...bid,
    type: 'bid',
    price: Number(bid.price).toFixed(pricePrecision - level),
    size: utils.fromWEI(baseSymbol, bid.size, amountPrecision),
    volume: utils.fromWEI(quoteSymbol, bid.volume, totalPrecision)
  }))

  return { asks, bids }
}

// direction: 
// asks() bids() 
function calcSummationCurve (items, direction, currentMarket) {
  //  depth 
  items = _.orderBy(items, item => -Number(item.price))
  // 
  if (direction === 'asc') {
    items = _.reverse(items)
  }

  let { amountPrecision, totalPrecision } = currentMarket
  let sumSize = 0
  let sumVolume = 0
  for (let item of items) {
    let { size, volume } = item
    sumSize += Number(size)
    sumVolume += Number(volume)
    item.sumSize = Number(sumSize.toFixed(amountPrecision))
    item.sumVolume = Number(sumVolume.toFixed(totalPrecision))
  }

  if (direction === 'asc') {
    items = _.reverse(items)
  }
  return items
}

export default {
  state: initDeepthState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchDepth (payload, { markets, depth }) {
      if (!markets.currentMarket) return

      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return
      
      this.update({
        asks: [],
        bids: [],
        loading: true,
      })

      const resp = await api.getDepth(marketPair, depth.level, 100)
      let { asks, bids } = formatFromWEI(resp, markets.currentMarket, depth.level)

      this.update({
        asks: calcSummationCurve(asks, 'asc', markets.currentMarket),
        bids: calcSummationCurve(bids, 'desc', markets.currentMarket),
        incrementalVersion: null,
        loading: false,
      })
      
      //  depth 
      dispatch.socket.unsubscribe([{
        topic: 'orderbook',
        unsubscribeAll: true
      }])
      dispatch.socket.subscribe([{
        topic: 'orderbook',
        market: marketPair,
        level: depth.level,
        // count: 
      }])

      scrollSellTableToBottom()
    },
    async setLevel (level) {
      this.update({level})
      dispatch.depth.fetchDepth()
    },
    async scrollSellTable () {
      scrollSellTableToBottom()
    },
    async incrementalUpdate (payload, { depth: depthState, markets }) {
      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      let { startVersion, endVersion, data: newDepth } = payload
      let { incrementalVersion, asks, bids, level } = depthState

      if (incrementalVersion) {
        // 2510, 2510 ---> 2511, 2511
        let expectVersion = incrementalVersion + 1
        if (startVersion < expectVersion) {
          return
        }
        
        if (startVersion > expectVersion) {
          // 
          dispatch.depth.fetchDepth()
          return
        }
      }
      
      // 
      newDepth.asks = newDepth.asks.map(depth => {
        let [ price, size, volume, count ] = depth
        return { price, size, volume, count }
      })
      newDepth.bids = newDepth.bids.map(depth => {
        let [ price, size, volume, count ] = depth
        return { price, size, volume, count }
      })
      newDepth = formatFromWEI(newDepth, markets.currentMarket, level)
      asks = _.chain([...newDepth.asks, ...asks]).unionBy(a => a.price).reject(a => Number(a.size) === 0).value()
      bids = _.chain([...newDepth.bids, ...bids]).unionBy(a => a.price).reject(a => Number(a.size) === 0).value()

      this.update({
        asks: calcSummationCurve(asks, 'asc', markets.currentMarket),
        bids: calcSummationCurve(bids, 'desc', markets.currentMarket),
        incrementalVersion: endVersion
      })
    },
  })
}