import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import numeral from 'numeral'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { Table, Button, Input, Icon, AutoComplete } from 'antd';
import utils from 'utils'

const { Option } = AutoComplete;

@injectIntl
class Pair extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fuzzy: '',
    }
  }

  componentDidMount() {
  }
  
  renderOption(item) {
    return (
      <Option key={item.category} text={item.category}>
        <div className="global-search-item">
          <span className="global-search-item-desc">
            Found {item.query} on
            <a
              href={`https://s.taobao.com/search?q=${item.query}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              {item.category}
            </a>
          </span>
          <span className="global-search-item-count">{item.count} results</span>
        </div>
      </Option>
    );
  }

  onChange(pagination, filters, sorter, extra) {
    let { order = 'market', field = 'descend' } = sorter
    this.props.updateFilterCond({
      sort: order,
      sortDirection: order === 'descend' ? 'desc' : 'asc'
    })
  }

  render() {
    let { persistData: { locale },  markets, setCurrentTab, setCurrentMarket, starMarket, intl } = this.props
    let { fuzzy } = this.state
    let pairs = _.chain(markets.currentMarkets)
                  .filter(({market}) => _.includes(_.upperCase(market), _.upperCase(fuzzy)))
                  .value()

    const columns = [
      {
        align: 'left',
        title: '',
        key: 'star',
        width: 20,
        render: (unused, record) => {
          let haveStar = _.find(markets.stars, {market: record.market})
          return (<Icon type="star" theme="filled"  style={{ fontSize: '14px', color: haveStar ? "#0064FF": "#3c3c50" }} />)
        },
        onCell: (record, rowIndex) => {
          return {
            onClick: event => {
              event.stopPropagation()
              starMarket(record)
            }
          }
        }
      },
      {
        align: 'left',
        title: intl.formatMessage({id: 'exchangePair'}),
        dataIndex: 'baseToken',
        width: 100,
        sorter: (a, b) => a.baseToken > b.baseToken,
        render: (unused, record) => (<Flex>{record.baseToken}<Text color="hint">/{record.quoteToken}</Text></Flex>),
      },
      {
        align: 'left',
        title: intl.formatMessage({id: 'pair.list.header.latest'}),
        dataIndex: 'close',
        width: 160,
        sorter: (a, b) => a.close - b.close,
        render: (close, record) => (
          <Flex>
            {close}
            <Text ml={2} color="hint">${utils.asLegalMoney(Number(record.close), record.quoteToken, 'USD')}</Text>
          </Flex>
        ),
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'pair.list.header.variation'}),
        dataIndex: 'variation',
        sorter: (a, b) => a.variation - b.variation,
        render: (variation) => (<Text color={variation >= 0 ? 'success' : 'failure'}>{numeral(variation).format('+0.00%')}</Text>),
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'pair.list.header.size'}),
        dataIndex: 'volume24h',
        sorter: (a, b) => a.volume24h - b.volume24h,
        render: (volume24h) => (<Text>{volume24h}</Text>),
      },
    ];

    return (
      <Box {...this.props} variant='noScrollbar' bg="backgroundTint" overflowY='auto'>
        <Flex alignItems='center' justifyContent='space-between' bg="background">
          <Flex alignItems='center' justifyContent='flex-start'>
            {
              markets.tabs.map(tab => (
                <Flex key={tab} width={78} height={40} alignItems='center' justifyContent='center' sx={{
                  color: 'white',
                  // borderBottom: '2px solid',
                  // borderColor: tab === markets.currentTab ? 'blue': 'background',
                  bg: tab === markets.currentTab ? 'backgroundTint': 'background',
                  cursor: 'pointer'
                }} onClick={() => setCurrentTab(tab)}>
                  <Text fontSize={0} fontWeight={'bold'}>
                    <FormattedMessage id={tab}></FormattedMessage>
                  </Text>
                </Flex>
              ))
            }
          </Flex>
          <Box px={14} width={160}>
            {/* <AutoComplete
              className="pairs-search"
              dataSource={pairs}
              onSelect={pair => {
                let [base, quote] = pair.split('-')
                setCurrentTab(quote)
                let currentMarket = _.find(markets.markets, {market: pair})
                setCurrentMarket(currentMarket)
              }}
              onSearch={value => this.setState({ fuzzy: value })}
              optionLabelProp="text"
            >
              <Input.Search
                className="pairs-search"
                placeholder=""
                onSearch={value => this.setState({ fuzzy: value })}
                style={{ height: 36, color: 'white' }}
              />
            </AutoComplete> */}
            <Input.Search
              className={`pairs-search ${fuzzy && 'has-content'}`}
              placeholder={intl.formatMessage({id: 'search'})}
              style={{ height: 36, color: 'white' }}
              allowClear={true}
              onChange={e => this.setState({ fuzzy: e.currentTarget.value })}
            />
          </Box>
        </Flex>
        <Box px={14}>
          <Table rowKey="market" columns={columns} dataSource={pairs} align="right"
            className="exchange-table"
            pagination={false}
            onRow={(record, rowIndex) => {
              return {
                onClick: event => {
                  setCurrentMarket(record)
                  // 
                  this.props.history.push(`/${locale}/trade/${record.market}`)
                  this.props.onSelected()
                }
              }
            }}
            // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
            onChange={(...params) => this.onChange(...params)}
          />
        </Box>
      </Box>
    )
  }
}

const mapStateToProps = ({markets, persistData}) => {
  return { markets, persistData };
};

const mapDispatchToProps = ({markets}) => {
  return {
    setCurrentTab: markets.setCurrentTab,
    setCurrentMarket: markets.setCurrentMarket,
    starMarket: markets.starMarket,
    updateFilterCond: markets.updateFilterCond
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Pair));