function withoutThrow(func) {
  return async (...params) => {
    try {
      return await func(...params)
    } catch (error) {
      // debugger
      notification['error']({
        style: {
          backgroundColor: 'white'
        },
        message: String(error),
      })
      return null
    }
  }
}

export function _parseResponse(response) {
  const data = response['data'];
  const resultInfo = data['resultInfo'];
  // const errorData = data['error'];

  // Some API endpoints in the old versions don't have resultInfo.
  if (resultInfo) {
    const { code, message } = resultInfo;
    if (code !== 0) {
      // 
      if (message === 'ERR_REST_INVALID_SIGNATURE') {
        dispatch.appState.update({
          notification: {
            type: 'error',
            message: 'notify.socket.subscribe.error.apikey'
          },
        })
        dispatch.dexAccount.logout()
      }

      // TODO: 
      let errorCodeMapping = {
        104208: ''
      }
      let errorMsgMapping = {
        'nounce too low': ''
      }
      let errorMessage = errorCodeMapping[code] || errorMsgMapping[message] || `${code} ${message}`
      throw Error(errorMessage);
    }
    return data;
  // } else if (errorData) {
  //   return data;
  } else {
    return data;
  }
}

export function parseResponse(response) {
  try {
    return _parseResponse(response)
  } catch (err) {
    // debugger
    notification['error']({
      style: {
        backgroundColor: 'white'
      },
      // message: 'Your deposit request failed. Please try again!',
      description: String(err),
    });
  }
}

let relayApi = axios.create({
  // timeout: 10000,
  baseURL: serverUrl,
})

relayApi.interceptors.response.use(function (response) {
  // Do something with response data
  return response
}, function (error) {
  if (error.message === "Network Error") {
    return Promise.reject('')
  }

  if (error.response) {
    return Promise.reject(error.response.data)
  } else {
    return Promise.reject({message: ''})
  }
})

async function getTokenBalance(owner, tokenAddress) {
  const data = {
    owner,
    token: tokenAddress
  };

  try {
    const response = await relayApi.post('/tokenBalances', data);
    const responseData = response['data'];
    if (responseData['error'] === undefined) {
      return responseData['balances'];
    } else {
      throw new Error(responseData['error']['message']);
    }
  } catch (e) {
    return false;
  }
}

