async function getDepth(market, level, limit) {
  const data = {
    market,
    level,
    limit
  };
  const tokens = market.split('-');
  const baseToken = tokens[0];

  try {
    const response = await relayApi.post('/depth', data);
    const responseData = response['data'];
    if (responseData['error'] === undefined) {
      const depth = responseData['depth'];
      const bids = depth['bids'];
      var updatedBids = [];
      for (var i = 0; i < bids.length; i = i + 1) {
        const bid = bids[i];
        var updatedBid = {
          price: bid.price,
          size: bid.size,
          volume: bid.volume,
          count: bid.count,
          sizeInNumber: config.fromWEI(baseToken, bid.size, 4)
        };
        updatedBids.push(updatedBid);
      }

      const asks = depth['asks'];
      var updatedAsks = [];
      for (var i = 0; i < asks.length; i = i + 1) {
        const ask = asks[i];
        var updatedAsk = {
          price: ask.price,
          size: ask.size,
          volume: ask.volume,
          count: ask.count,
          sizeInNumber: config.fromWEI(baseToken, ask.size, 4)
        };
        updatedAsks.push(updatedAsk);
      }

      return {
        bids: updatedBids,
        asks: updatedAsks
      };
    } else {
      return {
        bids: [],
        asks: []
      };
    }
  } catch (error) {
    return {
      bids: [],
      asks: []
    };
  }
}

async function getOrders({accountId, offset, limit, market, apiKey, statuses, side, start, end}) {
  const headers = {
    'X-API-KEY': apiKey
  };
  const data = {
    accountId,
    offset,
    limit,
    statuses,
    side,
    start,
    end
  };
  if (typeof market !== 'undefined') {
    data.market = market;
  }

  try {
    const response = await relayApi.post('/orders', data, {
      headers: headers,
    });

    const responseData = response['data'];
    if (responseData['error'] === undefined) {
      const orders = responseData['orders'];
      let updatedOrders = utils.formatOrders(orders)
      return {
        totalNum: responseData.totalNum,
        orders: updatedOrders
      }
    } else {
      throw new Error(responseData['error']['message']);
    }
  } catch (error) {
    throw error;
  }
}

async function cancelOrders(
  accountId,
  orderHash,
  clientOrderId,
  signed,
  apiKey
) {
  try {
    const data = {
      accountId: accountId,
      orderHash: orderHash,
      clientOrderId: clientOrderId
    };
    const signature = signed.Rx + ',' + signed.Ry + ',' + signed.s;
    const headers = {
      'X-API-KEY': apiKey,
      'X-API-SIG': signature
    };
    const response = await relayApi.post('/deleteOrders', data, {
      headers: headers,
    });

    return parseResponse(response);
  } catch (e) {
    throw e;
  }
}

async function getCandlestick(data) {
  try {
    const response = await relayApi.post('/candlestick', data)
    const responseData = response['data'];
    if (responseData['resultInfo']['code'] === 0) {
      return responseData['candles'];
    } else {
      throw new Error(responseData['resultInfo']['message']);
    }
  } catch (error) {
    // throw error;
    return []
  }
}

async function getCandlestickCombine(data) {
  let { start, end, interval } = data
  
  let resolution = ENUM_INTERVAL_TYPE.parseValueByText(interval)
  resolution = utils.formatResolution(resolution)
  let ms = resolution * maxCandlestickPerRequest * 60 * 1000

  let promises = []
  let periodEnd = end
  let periodStart = end - ms
  while (periodEnd > start) {
    let promise = getCandlestick({
      ...data,
      start: _.max([periodStart, start]),
      end: periodEnd
    })
    promises.push(promise)
    
    periodEnd -= ms
    periodStart -= ms
  }

  let resp = await Promise.all(promises)
  let candles = _.flatten(resp)
  return candles
}

