async function getRecommendedGasPrice() {
  try {
    const data = {};
    const response = await relayApi.post('/recommendedGasPrice', data)

    const responseData = response['data'];
    if (responseData['error'] === undefined) {
      let price = responseData['price']
      let minPrice = BigNumber(price)
      let normalPrice = minPrice.plus(2 * 1e9)
      let maxPrice = minPrice.times(30)
      return [minPrice, normalPrice, maxPrice]
    } else {
      throw new Error(responseData['error']['message']);
    }
  } catch (error) {
    throw error;
  }
}

async function getAllowance(owner, symbol) {
  let token = config.getTokenBySymbol(symbol);
  let tokenAddress = token.address;
  const data = {
    owner,
    token: tokenAddress
  };

  const response = await relayApi.post('/allowances', data)
  return response['data']['allowances'];
}

