import _ from 'lodash';
import axios from 'axios';
import { notification } from 'utils'
import appConfig from 'config/appConfig'
import { store } from 'store'

let srvApi = axios.create({
  // timeout: 10000,
  baseURL: appConfig.wedex.serverUrl,
})

srvApi.interceptors.request.use(function (config) {
  let rootState = store.getState()
  let { dexAccount: { address } } = rootState
  // config.headers.common['x-user-wallet-addr'] = '0x07f452344909f601aa8f9167e673d432404735d9'
  config.headers.common['x-user-wallet-addr'] = address
  return config
})

function getUserBillEggPrizeList (params) {
  let {address, ...restParams} = params
  // const headers = {
  //   'x-user-wallet-addr': address
  // };
  return srvApi.get(`user/billEggPrizes`, {
    params: restParams,
    // headers
  })
}

function postUserReadBillEggPrizeList (params) {
  let {address, ...restParams} = params
  // const headers = {
  //   'x-user-wallet-addr': address
  // };
  return srvApi.post(`user/billEggPrize/batchRead`, restParams, {
    // headers
  })
}

function formatExchanges (exchanges) {
  return _.map(exchanges, exchange => {
    let {
      standardContractName: baseToken,
      targetContractName: quoteToken,
      pricePrecision,
      amountPrecision,
      totalPrecision,
      maxLevel,
    } = exchange
    let baseContractId = exchange.standardContract.id
    let quoteContractId = exchange.targetContract.id

    return {
      market: _.toUpper(`${baseToken}-${quoteToken}`),
      baseToken,
      quoteToken,
      pricePrecision,
      amountPrecision,
      totalPrecision,
      maxLevel,
      baseContractId,
      quoteContractId
    }
  })
}
// 
let getAllExchanges = async (params) => {
  let resp = await srvApi.get(`user/exchanges`, {params})
  let exchanges = formatExchanges(resp.data.rows)
  return exchanges
}
let getChannelExchanges = async (params) => {
  let resp = await srvApi.get(`user/channelExchanges`, {params})
  let exchanges = formatExchanges(resp.data.rows)
  return exchanges
}
let getExchanges = async (params = {}) => {
  let { channelName } = params
  if (channelName) {
    return await getChannelExchanges(params)
  } else {
    return await getAllExchanges()
  }
}
let getContracts = async (params) => {
  let resp = await srvApi.get(`platform/contracts`, {params})
  let contracts = _.map(resp.data.rows, contract => {
    let {
      name: symbol,
      fullname: name,
      contractAddr : address,
      homepage: website,
      ...restProps
    } = contract
    return {
      symbol,
      name,
      address,
      website,
      ...restProps
    }
  })
  return contracts
}
let getContract = id => srvApi.get(`user/contracts/${id}`)

// 
let getSystemContractFee = params => srvApi.get(`user/system/contractFee`, {params})

// 
let getSystemBillValidTime = params => srvApi.get(`user/system/billValidTime`, {params})

let getArticleList = params => srvApi.get(`user/articles`, {params})
let getArticleListRecommended = params => srvApi.get(`user/articles`, {
  params: {
    recommended: true,
    ...params
  }
})
let getArticle = id => srvApi.get(`user/articles/${id}`)

export default {
  getUserBillEggPrizeList,
  postUserReadBillEggPrizeList,

  getExchanges,
  getContracts,
  getContract,
  getSystemContractFee,
  getSystemBillValidTime,

  getArticleList,
  getArticleListRecommended,
  getArticle
}