import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Pagination, Spin } from 'components';
import api from 'api'

import { Tabs, Table, Select, Checkbox } from 'antd';
const { TabPane } = Tabs;
const { Option } = Select;

@injectIntl
class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      list: []
    }
  }

  componentDidMount() {
    // this.props.fetchBalances()
    // this.props.fetchWalletBalances()
  }

  onChangeTab (key) {
    if (key === 'mine') {
    } else {
    }
  }

  fetchData = async function (pageParams) {
    this.setState({ loading: true })
    let { dexAccount } = this.props
    let resp = await api.getUserBillEggPrizeList({
      // address: '0x2bbfe5650e9876fb313d6b32352c6dc5966a7b68',
      address: dexAccount.address,
      ...pageParams,
      sort: 'billTime',
      sortDirection: 'desc',
      // TODO: delete
      startDate: '2020-01-02 18:00:00'
    })
    this.setState({ loading: false })
    return resp.data
  }.bind(this)
  
  onDataFetched = function (list) {
    console.info({list})
    this.setState({list})
  }.bind(this)

  render() {
    let { list } = this.state
    let { intl } = this.props

    const columns = [
      {
        title: intl.formatMessage({id: 'timestamp'}),
        dataIndex: 'billTime',
        key: 'billTime',
        width: 150,
        render: (billTime, record) => {
          return (
            <Flex>
              <Text>{moment(billTime).format('YYYY-MM-DD HH:mm:ss')}</Text>
            </Flex>
          )
        },
      },
      {
        title: intl.formatMessage({id: 'mine.award.egg.list.header.winner'}),
        dataIndex: 'winnerWalletAddr',
        key: 'winnerWalletAddr',
        width: 300
      },
      {
        title: intl.formatMessage({id: 'mine.award.egg.list.header.billEggName'}),
        dataIndex: 'billEggName',
        key: 'billEggName',
        width: 250,
      },
      
      {
        align: 'center',
        title: intl.formatMessage({id: 'mine.award.egg.list.header.amount'}),
        dataIndex: 'money',
        key: 'money',
        width: 100
      },
      {
        align: 'center',
        title: intl.formatMessage({id: 'tokenName'}),
        dataIndex: 'unit',
        key: 'unit',
        width: 100
      },
      
    ]

    return (
      <>
        <Box style={{position: 'relative'}}>
          <Tabs className="balance-tab" onChange={(key) => this.onChangeTab(key)} type="card">
            <TabPane tab={intl.formatMessage({id: 'mine.award.egg.tab.my'})} key="mine">
              <Spin spinning={this.state.loading} color="#0064FF">
                <Table rowKey="id" columns={columns} dataSource={list}
                  className="mine-table"
                  pagination={false}
                  // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
                />
              </Spin>
              <Pagination autoload={true} ref={pagination => this.pagination = pagination} fetchData={this.fetchData} onFetched={this.onDataFetched}></Pagination>
            </TabPane>
            {/* <TabPane tab="" key="all">
              <Table rowKey="id" columns={columns} dataSource={balances}
                className="mine-table"
                pagination={false}
                // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
              />
            </TabPane> */}
          </Tabs>
          {/* <Pagination ref={pagination => this.pagination = pagination} fetchData={this.fetchData} onFetched={this.onDataFetched}></Pagination> */}
        </Box>
      </>
    )
  }
}

const mapStateToProps = ({ dexAccount, balance, walletBalance }) => {
  return {
    dexAccount,
    balance,
    walletBalance
  };
};

const mapDispatchToProps = ({ balance, walletBalance, transfer }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))