import _ from 'lodash'
import numeral from 'numeral'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { TransferModal } from 'widgets/Dialog'
import utils from 'utils'
import { Spin } from 'components'
import api from 'api'

import Chart from 'bizcharts/lib/components/Chart';
import Geom from 'bizcharts/lib/components/Geom';
import Coord from 'bizcharts/lib/components/Coord';

import { Tabs, Table, Select, Checkbox, Menu, Dropdown, Divider } from 'antd';
const { TabPane } = Tabs;
const { Option } = Select;

const renderChart = _.memoize((overview) => {
  return (
    <Chart
      width={200}
      height={200}
      data={overview}
      padding={[0, 0, 0, 0]}
      forceFit
    >
      <Coord type={"theta"} radius={1} innerRadius={0.6} />
      <Geom
        type="intervalStack"
        position="percent"
        //  count count 
        color={['item', ['#0064FF', '#1890ff']]}
      >
      </Geom>
    </Chart>
  )
}, overview => `${overview[0].count}-${overview[1].count}`)

@injectIntl
class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filters: {
        tokenSymbol: '',
        hideSmall: false,
      },
      allMarkets: [],
      marketOptions: [],
    }
  }

  async componentDidMount() {
    let { markets } = this.props.markets

    // this.props.fetchBalances()
    this.props.fetchWalletBalances()
    this.setState({ allMarkets: markets })
  }

  onChangeTab (key) {
    if (key === 'dex') {
      //  socket 
      // this.props.fetchBalances()
    } else {
      this.props.fetchWalletBalances()
    }
  }

  renderOverview () {
    let { dexAccount, balance, walletBalance, intl } = this.props
    let balanceTotal = _.reduce(balance.list, (total, item) => {
      let usd = utils.asLegalMoney(Number(item.totalAmount), item.token.symbol, 'USD')
      return total + Number(usd)
    }, 0)
    let walletBalanceTotal = _.reduce(walletBalance.list, (total, item) => {
      let usd = utils.asLegalMoney(Number(item.amount), item.symbol, 'USD')
      return total + Number(usd)
    }, 0)
    let total = balanceTotal + walletBalanceTotal

    const overview = [
      {
        item: intl.formatMessage({id: 'mine.balance.tab.wallet'}),
        count: walletBalanceTotal,
        percent: total ? walletBalanceTotal / total * 100 : 0,
      },
      {
        item: intl.formatMessage({id: 'mine.balance.tab.exchange'}),
        count: balanceTotal,
        percent: total ? balanceTotal / total * 100: 0,
      },
    ];
    return (
      <Flex>
        <Box size={200} mr={60} mb={90}>
          { renderChart(overview) }
        </Box>
        <Flex flexDirection="column" flex={1}>
          <Text color="#323232" fontSize={20} fontWeight="bolder">
            <FormattedMessage id='mine.balance.overview.total' />
          </Text>
          <Flex mt={'5px'} alignItems="center">
            <Text color="#323232" fontSize={30} fontWeight="bolder">{utils.toTokenPrecision(total, 'USDT')} USD</Text>
            <Text fontSize={20} ml={30} fontWeight="bolder">{utils.asLegalMoney(total, 'USD', 'CNY')} CNY</Text>
          </Flex>
          <Text mt={'5px'} color="#323232">
            <FormattedMessage id='mine.balance.overview.addr' />: {dexAccount.address}
          </Text>
          <Flex mt={30} justifyContent="flex-start">
            <Box flex={1}>
              <Flex alignItems="center">
                <Box size={14} bg="#0064FF"></Box>
                <Text ml={'6px'} color="#969696">
                  <FormattedMessage id='mine.balance.overview.walletTotal' />
                </Text>
              </Flex>
              <Flex mt={'5px'} alignItems="center">
                <Text color="#323232" fontSize={14} fontWeight="bold">{utils.toTokenPrecision(walletBalanceTotal, 'USD')} USD</Text>
                <Text fontSize={14} ml={13}>{utils.asLegalMoney(walletBalanceTotal, 'USD', 'CNY')} CNY</Text>
              </Flex>
            </Box>
            <Box flex={1}>
              <Flex alignItems="center">
                <Box size={14} bg="#1890ff"></Box>
                <Text ml={'6px'} color="#969696">
                  <FormattedMessage id='mine.balance.overview.exchangeTotal' />
                </Text>
              </Flex>
              <Flex mt={'5px'} alignItems="center">
                <Text color="#323232" fontSize={14} fontWeight="bold">{utils.toTokenPrecision(balanceTotal, 'USD')} USD</Text>
                <Text fontSize={14} ml={13}>{utils.asLegalMoney(balanceTotal, 'USD', 'CNY')} CNY</Text>
              </Flex>
            </Box>
          </Flex>
        </Flex>
      </Flex>
    )
  }

  renderFilters () {
    // let { balance, walletBalance } = this.props
    // let balanceSymbols = balance.list.map(b => b.token.symbol)
    // let walletBalanceSymbols = walletBalance.list.map(b => b.symbol)

    // let symbols = _.union(balanceSymbols, walletBalanceSymbols)
    let { tokens: { list }, intl } = this.props
    const symbols = list.map(token => token.symbol)

    return (
      <Flex alignItems="center" style={{position: 'absolute', right: 0, zIndex: 1000}}>
        <Checkbox style={{color: '#323232'}} onChange={({ target }) => {
          this.setState(({ filters }) => {
            filters.hideSmall = target.checked
            return { filters }
          })
        }}>
          <FormattedMessage id='mine.balance.operations.hideSmall' />
        </Checkbox>
        <Select
          // size="small"
          placeholder={intl.formatMessage({id: 'mine.balance.operations.allToken'})}
          allowClear
          style={{ width: 140, color: '#323232' }}
          onChange={tokenSymbol => {
            this.setState(({ filters }) => {
              filters.tokenSymbol = tokenSymbol
              return { filters }
            })
          }}
        >
          {
            symbols.map(symbol => (
              <Option key={symbol} value={symbol}>{symbol}</Option>
            ))
          }
        </Select>
      </Flex>
    )
  }
  
  render() {
    let { filters, allMarkets, marketOptions } = this.state
    let { persistData: { locale }, history, balance, walletBalance, markets: marketsStore, setCurrentTab, setCurrentMarket, intl } = this.props
    let balances = balance.list
    let walletBalances = walletBalance.list

    if (filters.tokenSymbol) {
      balances = balances.filter(b => b.token.symbol === filters.tokenSymbol)
      walletBalances = walletBalances.filter(b => b.symbol === filters.tokenSymbol)
    }

    if (filters.hideSmall) {
      // 10usdt
      balances = _.reject(balances, item => {
        let usd = utils.asLegalMoney(Number(item.totalAmount), item.token.symbol)
        return usd < 10
      })
      walletBalances = _.reject(walletBalances, item => {
        let usd = utils.asLegalMoney(Number(item.amount), item.symbol)
        return usd < 10
      })
    }

    const walletColumns = [
      {
        title: intl.formatMessage({id: 'tokenName'}),
        dataIndex: 'tokenId',
        key: 'tokenId',
        render: (unused, record) => {
          return (
            <Flex>
              <Text>{record.symbol}</Text>
              <Text color="#969696">({record.name})</Text>
            </Flex>
          )
        },
      },
      {
        title: intl.formatMessage({id: 'mine.balance.wallet.list.header.amount'}),
        dataIndex: 'amount',
        key: 'amount',
        render: (unused, record) => {
          return <Text>{utils.toTokenPrecision(record.amount, record.symbol)}</Text>
        },
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'operations'}),
        dataIndex: '',
        key: 'ops',
        width: 200,
        render: (unused, record) => (
          <Flex alignItems="center" justifyContent="flex-end">
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={() => TransferModal({
                initialSymbol: record.symbol,
                initialType: 'deposit',
              })}
            >
              
              <FormattedMessage id="deposit" />
            </Text>
            <Divider type="vertical" />
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={async () => {
                history.push({ pathname: `/${locale}/mine/transfer`, data: {
                  initialSymbol: record.symbol,
                  initialSide: 'WITHDRAW',
                }})
              }}
            >
              <FormattedMessage id="mine.balance.operations.record" />
            </Text>
          </Flex>
        )
      }
    ]

    const dexColumns = [
      {
        title: intl.formatMessage({id: 'tokenName'}),
        dataIndex: 'tokenId',
        key: 'tokenId',
        width: 200,
        render: (unused, record) => {
          return (
            <Flex>
              <Text>{record.token.symbol}</Text>
              <Text color="#969696">({record.token.name})</Text>
            </Flex>
          )
        },
      },
      {
        title: intl.formatMessage({id: 'mine.balance.exchange.list.header.availAmount'}),
        dataIndex: 'availAmount',
        key: 'availAmount',
        width: 200,
        render: (availAmount, record) => {
          return <Text>{utils.toTokenPrecision(availAmount, record.token.symbol)}</Text>
        },
      },
      {
        title: intl.formatMessage({id: 'mine.balance.exchange.list.header.amountLocked'}),
        dataIndex: 'amountLocked',
        key: 'amountLocked',
        width: 200,
        render: (amountLocked, record) => {
          return <Text>{utils.toTokenPrecision(amountLocked, record.token.symbol)}</Text>
        },
      },
      {
        title: intl.formatMessage({id: 'mine.balance.exchange.list.header.totalAmount'}),
        dataIndex: 'totalAmount',
        key: 'totalAmount',
        width: 200,
        render: (totalAmount, record) => {
          return <Text>{utils.toTokenPrecision(totalAmount, record.token.symbol)}</Text>
        },
      },
      {
        align: 'right',
        title: intl.formatMessage({id: 'operations'}),
        dataIndex: '',
        key: 'ops',
        width: 250,
        render: (unused, record) => (
          <Flex alignItems="center" justifyContent="flex-end">
            <Dropdown overlay={
              <Menu onClick={({key: pair}) => {
                let [base, quote] = pair.split('-')
                setCurrentTab(quote)
                let currentMarket = _.find(marketsStore.markets, {market: pair})
                setCurrentMarket(currentMarket)
                history.push(`/${locale}/trade/${currentMarket.market}`)
              }}>
                {
                  marketOptions.map((menu) => (
                    <Menu.Item key={menu.market}>
                      <Text fontSize={[0, 0, 0]} color='hint'>{ menu.market }</Text>
                    </Menu.Item>
                  ))
                }
              </Menu>
              }
              placement="bottomCenter" overlayClassName="nav-menu"
            >
              <Text color="#0078FF" sx={{ cursor: 'pointer' }} onMouseEnter={() => {
                let markets = allMarkets.filter(market => market.baseToken === record.token.symbol || market.quoteToken === record.token.symbol)
                this.setState({marketOptions: markets})
              }}>
                <FormattedMessage id="mine.balance.operations.trade" />
              </Text>
            </Dropdown>
            <Divider type="vertical" />
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={() => TransferModal({
                initialSymbol: record.token.symbol,
                initialType: 'withdraw',
              })}
            >
              <FormattedMessage id="withdraw" />
            </Text>
            <Divider type="vertical" />
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={async () => {
                history.push({ pathname: `/${locale}/mine/transfer`, data: {
                  initialSymbol: record.token.symbol,
                  initialSide: 'DEPOSIT',
                }})
              }}
            >
              <FormattedMessage id="mine.balance.operations.record" />
            </Text>
          </Flex>
        )
      }
    ]

    return (
      <>
        { this.renderOverview() }
        <Box style={{position: 'relative'}}>
          { this.renderFilters() }
          <Tabs className="balance-tab" onChange={(key) => this.onChangeTab(key)} type="card">
            <TabPane tab={intl.formatMessage({id: 'mine.balance.tab.wallet'})} key="wallet">
              <Spin spinning={walletBalance.loading} color="#0064FF">
                <Table rowKey="symbol" columns={walletColumns} dataSource={walletBalances}
                  className="mine-table"
                  pagination={false}
                  // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
                />
              </Spin>
            </TabPane>
            <TabPane tab={intl.formatMessage({id: 'mine.balance.tab.exchange'})} key="dex">
              <Spin spinning={balance.loading} color="#0064FF">
                <Table rowKey="tokenId" columns={dexColumns} dataSource={balances}
                  className="mine-table"
                  pagination={false}
                  // locale={{emptyText: intl.formatMessage({id: 'noData'})}}
                />
              </Spin>
            </TabPane>
          </Tabs>
        </Box>
      </>
    )
  }
}

const mapStateToProps = ({ dexAccount, balance, walletBalance, rate, markets, tokens, persistData }) => {
  return {
    dexAccount,
    balance,
    walletBalance,
    rate,
    markets,
    tokens,
    persistData
  };
};

const mapDispatchToProps = ({ balance, walletBalance, transfer, markets }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
    setCurrentTab: markets.setCurrentTab,
    setCurrentMarket: markets.setCurrentMarket,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))