import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import utils from 'utils'
import api from 'api'

const mapStateToProps = ({ persistData, appState }) => {
  return { persistData, appState }
};
const mapDispatchToProps = ({ depth, persistData }) => {
  return {
  }
};

@withRouter
@connect(mapStateToProps, mapDispatchToProps)
export default class ActivityList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activity: {}
    }
  }

  componentDidMount() {
    let { id: activityId } = this.props.match.params
    this.fetchActivity(activityId)
  }

  fetchActivity = async function (activityId) {
    let resp = await api.getArticle(activityId)
    this.setState({ activity: resp.data })
  }

  render() {
    let { activity } = this.state
    return (
      <Flex bg="white" fontSize={14} sx={{
        minHeight: `calc(100vh - ${50 + 3}px)`,
        flexDirection: 'column',
        justifyContent: 'flex-start',
        alignItems: 'center',
        color: '#000'
      }}>
        <Text mt={40} fontSize={20} color="#323232" fontWeight="bold">{activity.title}</Text>
        <Box mt={40} maxWidth="1000px">
          <div dangerouslySetInnerHTML={{__html: activity.content}}></div>
        </Box>
      </Flex>
    )
  }
}
