import _ from 'lodash'
import api from 'api'
import utils from 'utils'
const BigNumber = require('bignumber.js');
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'

const initState = {
  loading: false,
  list: [],
  page: 1,
  pageSize: 10
}

function formatBalances (balances, tokens) {
  return balances.map(balance => {
    let { tokenId, totalAmount, amountLocked } = balance
    // 
    let token = _.find(tokens.list, { relayTokenId: String(tokenId) })
    if (!token) return

    balance.token = token
    let availAmount = BigNumber(totalAmount) - BigNumber(amountLocked)
    //  fromWEI,  fromWEI toFixed,
    // 
    balance.availAmount = BigNumber(availAmount).div("1e" + token.digits).decimalPlaces(token.precision).toNumber()
    balance.totalAmount = BigNumber(totalAmount).div("1e" + token.digits).decimalPlaces(token.precision).toNumber()
    balance.amountLocked = BigNumber(amountLocked).div("1e" + token.digits).decimalPlaces(token.precision).toNumber()
    return balance
  }).filter(balance => !!balance)
}

export default {
  state: initState,
  reducers: {
    initialize: (state, payload) => initState,
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchBalances (payload, { dexAccount, tokens }) {
      let { accountId, apiKey } = dexAccount
      if (!accountId || !apiKey) return
      this.update({ loading: true })

      let { skip, limit } = utils.formatPageParams(dexAccount)
      let balances = await api.getBalances(accountId, apiKey, skip, limit)
      if (!balances) {
        this.update({ loading: false })
        return
      }

      balances = formatBalances(balances, tokens)
      this.update({
        list: balances,
        loading: false
      })
    },
    // 
    startFetchBalance (payload, { dexAccount }) {
      let { accountId, state } = dexAccount
      if (!accountId || !ENUM_ACCOUNT_STATUS.LOGGED_IN.equals(state)) return
      console.info(`======  ======`)

      dispatch.balance.fetchBalances()
      dispatch.socket.unsubscribe([{
        topic: 'account',
        unsubscribeAll: true
      }])
      dispatch.socket.subscribe([{
        topic: 'account',
      }])
    },
    async onSocketReady (payload, { markets, depth }) {
      dispatch.balance.startFetchBalance()
    },
    async incrementalUpdate (payload, { balance, tokens }) {
      let balances = balance.list

      let [newBalance] = formatBalances([payload], tokens)
      let list = _.unionBy([newBalance], balances, b => b.tokenId)
      this.update({ list })
    },
  })
}