import React from 'react';
import { connect } from 'react-redux';
import numeral from 'numeral'
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import styled from '@emotion/styled'
import { Pair, TokenInfo } from 'widgets'
import { TVChartContainer } from './TVChartContainer';
import { DepthChartContainer } from './DepthChartContainer';
import { Dropdown, Icon, Menu } from 'antd'
import utils, { renderIf } from 'utils'
import api from 'api'
import { ENUM_INTERVAL_TYPE } from 'utils/enum'
import fullscreenIcon from 'icons/fullscreen.svg'
import chartTechnicIcon from 'icons/chartTechnic.svg'
import chartSettingIcon from 'icons/chartSetting.svg'

const units = ['USD', 'CNY']
const views = {
  DEPTH_VIEW: 0,
  TRADING_VIEW: 1
}
const tvLocalMapping = {
  cn: 'zh',
  us: 'en',
  kr: 'ko',
  ru: 'ru',
  jp: 'ja',
}

// https://emotion.sh/docs/styled#object-styles
const ViewTypeButton = styled(Text)(
  {
    border: '1px solid',
    cursor: 'pointer',
  },
  props => ({
    background: `${props.active ? '#1890ff' : ''}`,
    color: `${props.active ? 'white' : '#1890ff'}`,
    borderColor: `${props.active ? '#1890ff' : '#1890ff'}`,
    borderTopLeftRadius: `${props.left ? 2 : 0}px`,
    borderBottomLeftRadius: `${props.left ? 2 : 0}px`,
    borderTopRightRadius: `${props.right ? 2 : 0}px`,
    borderBottomRightRadius: `${props.right ? 2 : 0}px`,
    borderLeftWidth: `${props.right ? 0 : 1}px`,
    borderRightWidth: `${props.left ? 0 : 1}px`,
  })
)

const IntervalButton = styled(Text)(
  {
    width: '44px',
    height: '18px',
    marginRight: '3px',
    fontSize: '12px',
    lineHeight: '18px',
    background: 'rgba(255,255,255,0.08)',
    cursor: 'pointer',
    borderRadius: '1px',
    textAlign: 'center'
  },
  props => ({
    color: `${props.active ? '#0064FF' : '#78788C'}`,
  })
)

@injectIntl
class TVChart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      unit: 'USD',
      pairVisible: false,
      tokenInfoVisible: false,
      tokenInfo: {},
      selectedView: views.TRADING_VIEW,

      lineType: 'Candle',
      interval: ENUM_INTERVAL_TYPE.HOUR_1.getValue(),
      fullScreen: false,
      showIndicators: false,
      showSetting: false,
    }
  }

  componentDidMount() {
  }

  renderUnits () {
    return (
      <Menu onClick={({key}) => {
        console.info(key)
        this.setState({unit: key})
      }}>
      {
        units.map((unit) => (
          <Menu.Item key={unit}>
            <Text fontSize={[0, 0, 0]} color='hint'>{ unit }</Text>
          </Menu.Item>
        ))
      }
      </Menu>
    )
  }

  renderPairs () {
    return (
      <Box bg="background" sx={{
          border: '3px solid black'
        }}
        onClick={e => e.preventDefault()}
        // onClick={() => this.setState({ pairVisible: true })}
      >
        <Pair width={590} height={355} onSelected={() => this.setState({ pairVisible: false })} />
      </Box>
    )
  }

  async fetchTokenInfo () {
    let { markets: { currentMarket } } = this.props
    let resp = await api.getContract(currentMarket.baseContractId)
    this.setState({tokenInfo: resp.data})
  }

  renderTokenInfo () {
    return (
      <Box bg="background" sx={{
          border: '3px solid black'
        }}
        onClick={e => e.preventDefault()}
        // onClick={() => this.setState({ pairVisible: true })}
      >
        <TokenInfo tokenInfo={this.state.tokenInfo} />
      </Box>
    )
  }

  renderTvOps () {
    let { selectedView, fullScreen, showIndicators, showSetting } = this.state
    const { intl } = this.props

    let invetvalMapping = [
      // { label: '1', value: ENUM_INTERVAL_TYPE.MIN_1, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.5min'}), value: ENUM_INTERVAL_TYPE.MIN_5, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.15min'}), value: ENUM_INTERVAL_TYPE.MIN_15, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.30min'}), value: ENUM_INTERVAL_TYPE.MIN_30, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.1hr'}), value: ENUM_INTERVAL_TYPE.HOUR_1, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.2hr'}), value: ENUM_INTERVAL_TYPE.HOUR_2, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.4hr'}), value: ENUM_INTERVAL_TYPE.HOUR_4, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.12hr'}), value: ENUM_INTERVAL_TYPE.HOUR_12, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.1d'}), value: ENUM_INTERVAL_TYPE.DAY_1, },
      { label: intl.formatMessage({id: 'tvchar.resolutions.1w'}), value: ENUM_INTERVAL_TYPE.WEEK_1, },
    ]
    return (
      <Flex height={30} px={12} bg='background' alignItems="center" justifyContent="space-between">
        {
          renderIf(selectedView === views.TRADING_VIEW) (
            <Flex alignItems="center" justifyContent="space-between" >
              {/* <IntervalButton
                active={this.state.lineType === 'Line'}
                onClick={() => this.setState({
                  lineType: 'Line'
                })}
              >
                
              </IntervalButton> */}
              {
                _.map(invetvalMapping, item => {
                  let { lineType, interval } = this.state
                  let itemInterval = item.value.getValue()
                  return (
                    <IntervalButton key={item.label}
                      active={lineType === 'Candle' && interval === itemInterval}
                      onClick={() => {
                        const interval = item.value.getValue()
                        this.setState({
                          interval,
                          lineType: 'Candle'
                        }, () => {
                          // if (!window.localStorage) return;
                          // window.localStorage.setItem('bn.lineType', type);
                          // window.localStorage.setItem('bn.interval', interval);
                          // window.localStorage.setItem('bn.intervalName', intervalName);
                        });
                      }}
                    >
                      {item.label}
                    </IntervalButton>
                  )
                })
              }
              <Box mx={12} sx={{
                width:'0px',
                height: '16px',
                borderRight: '1px solid rgba(50,55,75,1)'
              }}></Box>
              <Box mr={2} lineHeight={'12px'} sx={{ cursor: 'pointer' }}
                onClick={() => this.setState({ showIndicators: !showIndicators })}
              >
                <Image width={12} height={12} lineHeight={'12px'} src={chartTechnicIcon} />
              </Box>
              <Box mr={2} lineHeight={'12px'} sx={{ cursor: 'pointer' }}
                onClick={() => this.setState({ showSetting: !showSetting })}
              >
                <Image width={12} height={12} lineHeight={'12px'} src={chartSettingIcon} />
              </Box>
              <Box mr={2} lineHeight={'12px'} sx={{ cursor: 'pointer' }}
                onClick={() => this.setState({ fullScreen: !fullScreen })}
              >
                <Image width={12} height={12} lineHeight={'12px'} src={fullscreenIcon} />
              </Box>
            </Flex>
          )
        }
        <Box></Box>
        {
          renderIf(!fullScreen)(
            <Flex alignItems="center" justifyContent="space-between" >
              <ViewTypeButton px={1} left
                active={selectedView === views.TRADING_VIEW}
                onClick={() => this.setState({selectedView: views.TRADING_VIEW})}
              >
                TradingView
              </ViewTypeButton>
              <ViewTypeButton px={1} right
                active={selectedView === views.DEPTH_VIEW}
                onClick={() => this.setState({selectedView: views.DEPTH_VIEW})}
              >
                <FormattedMessage id='tvchar.button.depth' />
              </ViewTypeButton>
            </Flex>
          )
        }
      </Flex>
    )
  }

  render() {
    let { markets: { currentMarket }, ticker: { dayInfo }, fetchCurrentMarkets, persistData } = this.props

    if (!currentMarket) return <></>

    let { baseToken, quoteToken, market, pricePrecision } = currentMarket
    let { high, low, volume, close, variation } = dayInfo

    return (
      <Box {...this.props}>
        <Flex height={50} px={12} py={'5px'} justifyContent="space-between">
          <Box pr={15}>
            <Flex mt={'4px'} alignItems="center">
              <Dropdown 
                visible={this.state.pairVisible}
                onVisibleChange={flag => {
                  this.setState({ pairVisible: flag })
                  if (flag === true) {
                    fetchCurrentMarkets()
                  }
                }}
                overlay={this.renderPairs()} trigger={['hover']} placement="bottomCenter">
                <Flex mr={'8px'} onClick={e => e.preventDefault()} sx={{cursor: 'pointer'}}>
                  <Text mr={'5px'} fontSize={16} lineHeight={'18px'} fontWeight={600}>
                    {baseToken}/{quoteToken}
                  </Text>
                  <Icon type="caret-down" style={{fontSize: '18px', color: '#646478'}} />
                </Flex>
              </Dropdown>
              <Dropdown 
                visible={this.state.tokenInfoVisible}
                onVisibleChange={flag => {
                  this.setState({ tokenInfoVisible: flag })
                  if (flag === true) {
                    this.fetchTokenInfo()
                  }
                }}
                overlay={this.renderTokenInfo()} trigger={['hover']} placement="bottomCenter">
                <Icon type="info-circle" style={{fontSize: '14px', color: '#646478', cursor: 'pointer'}} />
              </Dropdown>
            </Flex>
            <Flex color={variation >= 0 ? 'success' : 'failure'} mt={'0px'} alignItems="center" fontWeight={500}>
              <Text fontSize={14}>{close.toFixed(pricePrecision)}</Text>
              <Text width="60px" ml={'10px'} fontSize={14}>{numeral(variation).format('+0.00%')}</Text>
            </Flex>
          </Box>
          <Box px={15}>
            <Dropdown overlay={this.renderUnits()} trigger={['hover']} placement="bottomCenter" overlayClassName="nav-menu">
              <Flex mt={'3px'} alignItems="center">
                <Text color='#646478' mr={'5px'}><FormattedMessage id='tvchar.baseInfo.unit' />:</Text>
                <Text mr={'5px'}>{this.state.unit}</Text>
                <Icon type="caret-down" />
              </Flex>
            </Dropdown>
            <Text mt={'2px'} color='#FFFFFF'>{utils.asLegalMoney(close, quoteToken, this.state.unit)}</Text>
          </Box>
          <Box px={15}>
            <Text color='#646478' mt={'3px'} mr={'5px'}><FormattedMessage id='tvchar.baseInfo.high' /></Text>
            <Text color='#FFFFFF' mt={'2px'}>{high}</Text>
          </Box>
          <Box px={15}>
            <Text color='#646478' mt={'3px'} mr={'5px'}><FormattedMessage id='tvchar.baseInfo.low' /></Text>
            <Text color='#FFFFFF' mt={'2px'}>{low}</Text>
          </Box>
          <Box px={15}>
            <Text color='#646478' mt={'3px'} mr={'5px'}><FormattedMessage id='tvchar.baseInfo.volumn' /></Text>
            <Text color='#FFFFFF' mt={'2px'}>{volume} {quoteToken}</Text>
          </Box>
          <Box flex={1}></Box>
        </Flex>

        <Box sx={this.state.fullScreen ? {
          position: 'fixed',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          zIndex: 2
        } : {}}>
          { this.renderTvOps() }
          {
            renderIf(this.state.selectedView === views.TRADING_VIEW)(
              <TVChartContainer style={{height: `calc(${this.props.height}px - 50px - 30px)`}}
                locale={tvLocalMapping[persistData.locale]}
                symbol={market}
                lineType={this.state.lineType}
                interval={this.state.interval}
                showIndicatorsDialog={this.state.showIndicators}
                showSettingDialog={this.state.showSetting}
                visible={this.state.selectedView === views.TRADING_VIEW}
                fullScreen={this.state.fullScreen}
              />
            )
          }
          {
            renderIf(this.state.selectedView === views.DEPTH_VIEW)(
              <DepthChartContainer style={{height: `calc(${this.props.height}px - 50px - 30px)`}}
                symbol={market}
                visible={this.state.selectedView === views.DEPTH_VIEW}
                fullScreen={this.state.fullScreen}
              />
            )
          }
        </Box>
      </Box>
    )
  }
}

const mapStateToProps = ({ markets, trade, ticker, persistData }) => {
  return {
    markets,
    trade,
    ticker,
    persistData
  };
};

const mapDispatchToProps = ({ markets }) => {
  return {
    fetchCurrentMarkets: markets.fetchCurrentMarkets
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TVChart);