import _ from 'lodash'
import { ENUM_INTERVAL_TYPE } from 'utils/enum'
import api from 'api'
import utils from 'utils'

const initState = {
  dayInfo: {
    "high": 0,
    "low": 0,
    "size": 0,
    "volume": 0
  },
  tvSubscribers: []
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchDayInfo (payload, { markets, candleStick }) {
      if (!markets.currentMarket) return

      var candles = await api.getCandlestick({
        market: markets.currentMarket.market,
        interval: '1d',
      })
      if (!candles || candles.length === 0) return

      let dayInfo = _.pick(candles[0], ['high', 'low', 'size', 'volume'])
      let { baseToken: baseSymbol } = markets.currentMarket
      dayInfo.size = utils.fromWEI(baseSymbol, dayInfo.size)

      this.update({
        dayInfo: dayInfo,
      })
    },
    async subscribeBars (payload, { candleStick: {tvSubscribers} }) {
      let {subscribeUID, onRealtimeCallback, resolution, symbolInfo} = payload

      let market = symbolInfo['name'];
      resolution = ENUM_INTERVAL_TYPE.toText(resolution)
      let topic = `candlestick&${market}&${resolution}`

      let newSub = {
        lastBar: null,
        subscribeUID,
        topic,
        realtimeCb: onRealtimeCallback,
        resolution: resolution,
        symbolInfo: symbolInfo
      }
      tvSubscribers = _.unionBy(tvSubscribers, [newSub], subscribeUID)
      this.update({tvSubscribers})

      dispatch.socket.unsubscribe([{
        topic: 'candlestick',
        unsubscribeAll: true
      }])
      dispatch.socket.subscribe([{
        topic: 'candlestick',
        market,
        interval: resolution,
      }])
    },
    async unsubscribeBars (subscriberUID, { candleStick: {tvSubscribers} }) {
      tvSubscribers = _.reject(tvSubscribers, sub => sub.subscriberUID = subscriberUID)
      this.update({tvSubscribers})
    },
    async incrementalUpdate(payload, { candleStick: {tvSubscribers} }) {
      let { topic: topicInfo, data } = payload
      let { topic, market, interval } = topicInfo
      
      let subscriberTopic = `candlestick&${market}&${interval}`
      let subscriber = tvSubscribers.find(sub => _.toUpper(sub.topic) === _.toUpper(subscriberTopic))
      if (!subscriber) return

      // let lastBar = sub.lastBar

      let quoteSymbol = subscriber.symbolInfo.name.split('-')[1]
      let [start, count, open, close, high, low, size, volume] = data
      let newCancle = {
        time: Number(start),
        high: Number(high),
        low: Number(low),
        open: Number(open),
        close: Number(close),
        volume: utils.fromWEI(quoteSymbol, volume)
      }
      console.info('', newCancle)
      subscriber.realtimeCb(newCancle)
      // sub.lastBar = lastBar
    }
  })
}