import moment from "moment"
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { injectIntl, FormattedMessage  } from 'react-intl'
import { Modal, Input, Form, Button, Checkbox } from 'antd'
import {
  Box,
  Flex,
  Text
} from 'rebass'
import funcfy from './funcfyDialog'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import utils, { notification } from 'utils'
import { FormErrorInfo } from 'widgets/Common'
import { UpsertPasswordModal } from '../Dialog'
import api from 'api'

@injectIntl
class Widget extends Component {
  constructor(props) {
    super(props)

    this.state = {
      visible: false
    }

    let { intl } = props
    this.errorInfos = [
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.upperCase'}),
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.lowerCase'}),
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.number'}),
      intl.formatMessage({id: 'modal.upsertPwd.errorInfo.min'}),
    ]
  }

  componentDidMount () {
    this.setState({ visible: true })
    this.checkInput()
  }

  async checkInput () {
    // onChangeonChange
    await utils.waitFor(50)

    // 
    let password = this.props.form.getFieldProps('password').value
    if (!password) {
      this.setState({errors: this.errorInfos})
      return 
    }

    let errors = this.props.form.getFieldsError()
    errors = _.chain(errors)
      .values()
      .flatten()
      .filter(error => error)
      .value()
    this.setState({errors})
  }

  async submit (e) {
    let { dexAccount, intl } = this.props

    e.preventDefault();
    let validateFields = utils.promisefy(this.props.form.validateFields)

    let values
    try {
      values = await validateFields()
    } catch(err) {
      console.info(err)
      return
    }
    let { password, remember } = values

    const passwordHash = utils.genPasswordHash(dexAccount.address, password);
    const relayAccount = await api.lightconeGetAccount(dexAccount.address);
    console.info(password)
    let valid = utils.checkPassword(passwordHash, relayAccount)
    if (!valid) {
      notification['success']({
        message: (
          <div>
            <p>{intl.formatMessage({id: "notify.signIn.error.mismatch"})}</p>
          </div>
        )
      });
      return
    }

    let accountId = relayAccount.accountId
    let apiKey = await utils.genApiKey(accountId, passwordHash)
    await this.props.updateAccount({
      accountId,
      passwordHash,
      apiKey,
      rememberMeStartTime: moment().format('YYYY-MM-DD HH:mm:ss'),
      rememberMeMinutes: (remember ? 24 : 1) * 60,
    })

    if (relayAccount.isFreeze) {
      this.props.updateAccount({ state: ENUM_ACCOUNT_STATUS.LOCKED.getValue()})
      notification['success']({
        message: (
          <div>
            <p>{intl.formatMessage({id: "notify.signIn.error.locked"})}</p>
          </div>
        )
      });
    } else {
      this.props.updateAccount({ state: ENUM_ACCOUNT_STATUS.LOGGED_IN.getValue()})
      this.props.startFetchBalance()
      this.props.fetchOrders()

      notification['success']({
        message: intl.formatMessage({id: "notify.signIn.success"})
      });
    }
    this.loginSunccess()
  }

  async loginSunccess () {
    this.setState({visible: false})
    this.props.onSucess()
    this.props.onDestory()
  }

  render () {
    let { dexAccount: { state }, intl } = this.props

    let modalTitle = ENUM_ACCOUNT_STATUS.UNCREATED.equals(state) ? 
      intl.formatMessage({id: `modal.signin.title.create`}) : 
      intl.formatMessage({id: `modal.signin.title.verify`})

    const { getFieldDecorator, getFieldsValue } = this.props.form;
    let { remember } = getFieldsValue()
    
    const formItemLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
    };
    const noLabelLayout = {
      wrapperCol: { span: 20, offset: 4 },
    };
    return (
      <Modal 
        // bodyStyle={{backgroundColor: '#363636', color: '#d0d0d0'}}
        title={modalTitle}
        visible={this.state.visible}
        maskClosable={true}
        onOk={() => {

        }}
        onCancel={() => {
          this.setState({visible: false})
          this.props.onFail()
          this.props.onDestory()
        }}
        footer={null}
        width={550}
        className="password-modal"
      >
        <Box pt={20} pb={47} justifyContent="center">
          <Box width={440} mx="auto">
            <Form className="upsert-password-form" onSubmit={(e) => {
              utils.withLoading(() => this.submit(e))
            }}>
              <Box>
                <Form.Item {...formItemLayout} label={intl.formatMessage({id: `password`})} hasFeedback={false}>
                  {getFieldDecorator('password', {
                    rules: [
                      { required: true, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.required'}) },
                      { pattern: /[A-Z]+/, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.upperCase'}) },
                      { pattern: /[a-z]+/, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.lowerCase'}) },
                      { pattern: /[0-9]+/, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.number'}) },
                      { min: 8, message: intl.formatMessage({id: 'modal.upsertPwd.errorInfo.min'}) },
                    ],
                  })(
                    <Input.Password size="large" onChange={() => this.checkInput()} />
                  )}
                </Form.Item>
              </Box>
              <Box mt={26}>
                <Form.Item {...noLabelLayout}>
                  <FormErrorInfo errors={this.state.errors} items={this.errorInfos} />
                </Form.Item>
              </Box>
              <Box mt={26}>
                <Form.Item {...noLabelLayout}>
                  <Button size="large" style={{width: '100%'}} type="primary" htmlType="submit" >
                    <FormattedMessage id="confirm" />
                  </Button>
                  <Box mt={30}></Box>
                  <Button size="large" style={{width: '100%'}} ghost type="primary" onClick={() => {
                    this.props.onSucess()
                    this.props.onDestory()
                    UpsertPasswordModal()
                  }}>
                    <FormattedMessage id="modal.signin.label.forget" />
                  </Button>
                </Form.Item>
              </Box>
            </Form>
          </Box>
        </Box>
      </Modal>
    )
  }
}

const WrappedForm = Form.create({ name: 'upsert_password_form' })(Widget);

const mapStateToProps = ({ metaMask, dexAccount }) => {
  return {
    metaMask,
    dexAccount
  };
};
const mapDispatchToProps = ({ dexAccount, balance, order }) => {
  return {
    updateAccount: dexAccount.updateAccount,
    startFetchBalance: balance.startFetchBalance,
    fetchOrders: order.fetchOrders,
  };
};
let connectedWidget = connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(WrappedForm)

export default function promisefy (props) {
  return new Promise((resolve, reject) => {
    funcfy(connectedWidget, {
      ...props,
      onSucess: resolve,
      onFail: reject,
    })
  })
}